/* HEADER */

.site-header {
  position: sticky; top: 0; left: 0; right: 0;
  width: 100%;
  z-index: 99;
  background:$white;

  @media only screen and (min-width: 1250px) {
    position: sticky; top: 0; left: 0; right: 0;
  }
   
  .main-header {
    height: 5.0rem;
    box-shadow: 0 0 10px $shadow-color;
    transition: all .2s linear;
    background-color: $dark-pink;

    @media only screen and (min-width: 1250px) {
      height: 7.6rem;
      transition: all .2s linear;
    }
  }

  .main-header__container {
    margin-right: auto;
    margin-left: auto;
    padding-left: 2rem;
    padding-right: 2rem;
    position: relative;
    display: flex;
    height: 100%;
    align-items: center;
  }

  @media only screen and (min-width: 992px) {
    .main-header__container {
      max-width: 115rem;
    }
  }
  
  @media only screen and (min-width: 1250px) {
    .main-header__container {
      max-width: 125rem;
    }
  }
  
  @media only screen and (min-width: 1440px) {
    .main-header__container {
      max-width: 134rem;
    }
  }
  
  @media only screen and (min-width: 1650px) {
    .main-header__container {
      max-width: 144rem;
    }
  }
  
  @media only screen and (min-width: 1920px) {
    .main-header__container {
      max-width: 170rem;
    }
  }

  .site-header__logo-link {
    position: relative;

    .overlay {
      position: absolute;
      height: 100%;
      width: 100%;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      opacity: 0;
      transition: .4s ease;
      background: $dark-pink;
    }

    &:hover .overlay {
      opacity: 1;
    }
  }

  .site-header__logo {
    display: block;
    height: auto;
    width: 17rem;
  }
  
  @media only screen and (min-width: 1250px) {
    .site-header__logo {
      width: 17rem;
      margin-right: 3rem;
    }
  }

  .site-header__logo-square {
      display: inline;
      margin-left: auto;
      margin-right: auto;
      margin-bottom: 0.5rem;
      height: auto;
      width: 8rem;
    }

  @media only screen and (min-width: 1250px) {
    .site-header__logo-square {
      margin-left: 5rem;
      margin-right: 3rem;
      margin-bottom: 0.5rem;
      margin-top: -0.6rem;
      width: 8rem;
    }
  }

  .main-nav {
    display: none;
  }
  
  @media only screen and (min-width: 1250px) {
    .main-nav {
      display: block;
      margin-top: 0.2rem;
    }
  }

  .primary-nav {
    font-size: 0;
    list-style: none;
    margin: 0;
    padding: 0;
  }
  
  .primary-nav__item {
    display: inline-block;
    font-size: 1.6rem;
    font-weight: 700;
    line-height: 1;
    margin: 0 2.5rem 0 0;
    padding: 0;
    position: relative;
    transition: all .2s linear;
  }
  
  @media only screen and (min-width: 1440px) {
    .primary-nav__item {
      margin: 0 3.5rem 0 0;
      transition: all .2s linear;
    }
  }
  
  .primary-nav__item:last-child {
    margin-right: 0;
    transition: all .2s linear;
  }
  
  .primary-nav__link {
    color: $white;
    text-decoration: none;
    padding: 0.2rem 0;
  }
  
  .primary-nav__item--current .primary-nav__link {
    border-bottom: 0.2rem solid $white;
    text-decoration: none;
  }

  .primary-nav__hover1:hover {
    border-bottom: 0.2rem solid $white;
    text-decoration: none;
  }

  .primary-nav__hover2:hover {
    border-bottom: 0.2rem solid $black;
    text-decoration: none;
  }

  .main-header__right {
    margin-left: auto;
    display: flex;
    position: relative;
  }

  .header-sidebar-nav {
    cursor: pointer;
    display: block;
    color: $white;
    top: -0.2rem;
    position: relative;
  }

  .header-sidebar-nav:hover {
    opacity: 0.75;
    filter: alpha(opacity=75);
  }
  
  .header-sidebar-nav-toggle__icon {
    width: 5rem;
    display: block;
  }

  .secondary-header {
    border-bottom: 1px solid #eee;
    height: 4.5rem;
    display: none;
    transition: all 0.2s linear;
  }
  
  @media only screen and (min-width: 1250px) {
    .secondary-header {
      display: block;
      transition: all 0.2s linear;
    }
  }

  .secondary-header__container {
    margin-right: auto;
    margin-left: auto;
    padding-left: 2rem;
    padding-right: 2rem;
    position: relative;
    display: flex;
    height: 100%;
    align-items: center;
  }
  
  @media only screen and (min-width: 992px) {
    .secondary-header__container {
      max-width: 96.5rem;
    }
  }
  
  @media only screen and (min-width: 1250px) {
    .secondary-header__container {
      max-width: 120rem;
    }
  }
  
  @media only screen and (min-width: 1440px) {
    .secondary-header__container {
      max-width: 130rem;
    }
  }
  
  @media only screen and (min-width: 1650px) {
    .secondary-header__container {
      max-width: 144rem;
    }
  }
  
  @media only screen and (min-width: 1920px) {
    .secondary-header__container {
      max-width: 170rem;
    }
  }

  .secondary-nav {
    font-size: 0;
    float: left;
    list-style: none;
    margin: 0;
    overflow: hidden;
    padding: 0;
    position: relative;
  }
  
  .secondary-nav__item {
    display: inline-block;
    font-size: 1.4rem;
    font-weight: 600;
    line-height: 1.3;
    margin: 0 2rem 0 0;
    padding: 0;
  }
  
  @media only screen and (min-width: 1650px) {
    .secondary-nav__item {
      margin-right: 3.5rem;
    }
  }
  
  @media only screen and (min-width: 768px) {
    .secondary-nav__item:last-child {
      margin-right: 0;
    }
  }
  
  .secondary-nav__link {
    display: block;
    position: relative;
    text-decoration: none;
    z-index: 1;
    padding: 1.4rem 0 0;
    border-bottom: 0.2rem solid transparent;
    height: 4.5rem;
    color: $dark-pink;
  }
  
  .secondary-nav__link:hover,.secondary-nav__item--current .secondary-nav__link {
    border-bottom: 1px solid;
    text-decoration: none;
    color: $dark-pink;
  }

  /* CSS for Slide Out */
  @import "header/slideOut/slideOut";

}