/* LEFT TOC STYLING */

/* Filter Bar */
.left-sidebar__visually-hidden {
  position: absolute;
  overflow: hidden; 
  height: 1px;
  width: 1px; 
  padding: 0;
  margin: -1px;
  border: 0;
  clip: rect(1px, 1px, 1px, 1px);
  clip-path: inset(50%);
  word-wrap: normal;
}

/* Navigation Menu */
.lhs-tree-menu-toc{
  margin: 0px;
  padding-left: 0px;
}

ul.lhs-tree-menu-toc {
  position: relative;
  font-size: 1.2rem;
  list-style: none;
  ul {
    position: relative;
    font-size: 1.2rem;
    list-style: none;
  }
  & > ul{
    padding: 0px 0px;
    text-decoration: none;
    & .treegroup:not(.is-expanded){
      display: none;
    }
  }
  & > ul ul {
    padding: 0px 0px;
    margin-left: 1.5rem;
    text-decoration: none;
  }
  a {
    display: block;
    padding: 0px 0px;
    margin: 0px 0px;
    color: $body-color;
    text-decoration: none;
  }
  li{
    //display: flex;
    //align-items: center;
    min-height: 2.8rem;
    padding: 2px 0rem 2px 1.6rem;
    margin: 0;
    color: $body-color;
    text-decoration: none;
    cursor: pointer;
    border-top-right-radius: 8px;
    border-bottom-right-radius: 8px;
    font-size: 1.2rem;

    &.is-active, &.is-active a {
      font-weight: 600;
      background-color: $pearl-pink;
      color: $dark-pink;
    }
    &:hover:not(.is-active){
      background-color: $mistygrey;
    }
    &:hover:not(.is-active) a {
      background-color: $mistygrey;
      text-decoration: underline;
    }
  }

  /* Chevron */
  .chevron{
    height:0.8rem;
    width: 0.8rem;
  }

  .tree-expander{
    display: block;
    position: relative;
    padding-left: 0px;
    padding-top: 0px;
    padding-bottom: 0px;
    cursor: pointer;
    user-select: none;
  }

  .tree-indicator{
    display: inline-block;
    position: absolute;
    top: 0.35rem;
    left: -1.5em;
    line-height: 16px;
    font-size: 0.96rem;
    text-align: center;
    color: $darkgrey;
    transition: transform 0.15s ease-in-out;
    transform: rotate(0deg);
  }

  .treeitem.is-expanded > .tree-expander > .tree-indicator {
    transform: rotate(90deg);
  }

}


/* Back Button */
  #contentEarlierPage {

      .iconify.medium {
        font-size: 2.6rem;
        line-height: 28px;
      }
  }

  %button{
    display: flex;
    align-items: center;
    max-width: 5.0rem;
    padding: 1.12rem;
    background-color: $dark-pink;
    color: $white;
    box-shadow:0 3px 12px 0 $shadow-color;
    transition-property: all;
    transition-duration: 120ms;
    transition-timing-function: linear;
    transition-delay: 0s;
  }

  %buttonHover{
    background-color: $white; 
    color: $dark-pink;
    border: 1px solid $dark-pink;
    -webkit-box-shadow:0 4px 14px 0 $shadow-color;
    box-shadow:0 4px 14px 0 $shadow-color;
    border-radius: 2px; 
  }

  %isFaded{
    background-color: $tingegrey;
    color: $grey;
  }

  %isFadedHover{
    background-color: $tingegrey;
    color: $grey;
    border: 1px solid $darkish-grey;
    -webkit-box-shadow:0 4px 14px 0 $shadow-color;
    box-shadow:0 4px 14px 0 $shadow-color;
  }

  #contentEarlierPage{
    @extend %button;
    margin-top: 0.85rem;
    margin-left: 3.0rem;
    &:hover{
        @extend %buttonHover; 
    }
    &.is-faded{
    @extend %isFaded; 
        &:hover{
            @extend %isFadedHover; 
        }
    }

  }


