#contentNavBottom {
    display: flex;
    justify-content: space-between;
    margin-top: 1.6rem;
    margin-right: 0rem;
    margin-left: 0rem;

    .iconify.medium {
        font-size: 2.6rem;
        line-height: 28px;
    }
}

%page{
    display: flex;
    align-items: center;
    margin-top: 1.92rem;
    padding: 1.12rem;
    background-color: $dark-pink;
    color: $white;
    transition-property: all;
    transition-duration: 120ms;
    transition-timing-function: linear;
    transition-delay: 0s;
}

%pageHover{
    background-color: $white; 
    color: $dark-pink;
    border: 1px solid $dark-pink;
    -webkit-box-shadow:0 4px 14px 0 $shadow-color;
    box-shadow:0 4px 14px 0 $shadow-color;
    border-radius: 2px; 
}

%isFaded{
    background-color: $tingegrey;
    color: $grey;
}

%isFadedHover{
    background-color: $tingegrey;
    color: $grey;
    border: 1px solid $darkish-grey;
    -webkit-box-shadow:0 4px 14px 0 $shadow-color;
    box-shadow:0 4px 14px 0 $shadow-color;
}

#contentNextPage{
    @extend %page;
    margin-right: 0.32rem;
    &:hover{
        @extend %pageHover; 
    }
    &.is-faded{
    @extend %isFaded; 
        &:hover{
            @extend %isFadedHover; 
        }
    }
}

#contentPreviousPage{
    @extend %page;
    margin-left: 0.32rem;
    &:hover{
        @extend %pageHover; 
    }
    &.is-faded{
    @extend %isFaded; 
        &:hover{
            @extend %isFadedHover; 
        }
    }

}
