  /* UMB GRID */

  .umb-grid h1,.umb-grid h2,.umb-grid h3,.umb-grid h4,.umb-grid h5,.umb-grid h6 {
    color: $black;
    font-size: 2rem;
    font-weight: 800;
    line-height: 1.6;
    margin-top: 0;
    margin-bottom: 1rem;
    position: relative;
  }
  
  @media only screen and (min-width: 768px) {
    .umb-grid h1,.umb-grid h2,.umb-grid h3,.umb-grid h4,.umb-grid h5,.umb-grid h6 {
      margin-top: 0;
      margin-bottom: 0;
    }
  }
  
  .umb-grid h1 a,.umb-grid h2 a,.umb-grid h3 a,.umb-grid h4 a,.umb-grid h5 a,.umb-grid h6 a {
    text-decoration: none;
  }
  
  .umb-grid h1 a:hover,.umb-grid h2 a:hover,.umb-grid h3 a:hover,.umb-grid h4 a:hover,.umb-grid h5 a:hover,.umb-grid h6 a:hover {
    text-decoration: underline;
  }
  
  .umb-grid .grid-section h1:first-child,.umb-grid .grid-section h2:first-child,.umb-grid .grid-section h3:first-child,.umb-grid .grid-section h4:first-child,.umb-grid .grid-section h5:first-child,.umb-grid .grid-section h6:first-child {
    margin-top: 0;
  }
  
  .umb-grid h1,.umb-grid h2,.umb-grid h3 {
    font-size: 2rem;
    font-weight: 700;
  }
  
  @media only screen and (min-width: 768px) {
    .umb-grid h1,.umb-grid h2,.umb-grid h3 {
      font-size: 2.2rem;
    }
  }
  
  .umb-grid h2 {
    margin: 5rem 0 3rem;
  }
  
  .umb-grid h3 {
    margin: 5rem 0 1rem;
  }
  
  .umb-grid .inline-block+h2,.umb-grid .inline-block+h3 {
    margin-top: 0;
  }
  
  .umb-grid h4,.umb-grid h5,.umb-grid h6 {
    font-size: 1.4rem;
  }
  
  @media only screen and (min-width: 768px) {
    .umb-grid h4,.umb-grid h5,.umb-grid h6 {
      font-size: 2rem;
    }
  }

  .umb-grid p {
    color: $black;
    font-weight: 400;
    line-height: 1.8;
    margin: 0;
    margin-bottom: 3rem;
    font-size: 1.6rem;
  }
  
  @media only screen and (min-width: 768px) {
    .umb-grid p {
      font-size: 1.8rem;
    }
  }

  .umb-grid blockquote {
    font-weight: 700;
    margin: 6rem 0 8rem;
    position: relative;
  }
  
  .umb-grid blockquote p {
    color: $black;
    font-weight: 700;
    font-size: 4rem;
    line-height: 1.2;
    margin-bottom: 0;
    position: relative;
  }
  
  @media only screen and (min-width: 768px) {
    .umb-grid blockquote p {
      font-size: 4rem;
    }
  }
  
  .umb-grid blockquote cite {
    color: $black;
    display: block;
    font-style: normal;
    font-size: 1.4rem;
    font-weight: 600;
    line-height: 1.8;
    margin: 2rem 0 0;
  }
  
  .umb-grid a {
    color: $dark-pink;
    text-decoration: none;
  }
  
  .umb-grid a:hover {
    text-decoration: underline;
  }

  .umb-grid ul,.umb-grid ol {
    margin: 0 0 3rem 2rem;
    padding: 0;
  }
  
  @media only screen and (min-width: 1250px) {
    .umb-grid ul,.umb-grid ol {
      margin-bottom: 4rem;
    }
  }
  
  .umb-grid ul li,.umb-grid ol li {
    color: $black;
    font-weight: 400;
    line-height: 1.8;
    margin: 0;
    margin-bottom: 0.6rem;
    font-size: 1.6rem;
  }
  
  @media only screen and (min-width: 768px) {
    .umb-grid ul li,.umb-grid ol li {
      font-size: 1.8rem;
    }
  }
  
  .umb-grid ul li:last-child,.umb-grid ol li:last-child {
    margin-bottom: 0;
  }
  
  .umb-grid ul li a,.umb-grid ol li a {
    color: $dark-pink;
    text-decoration: none;
  }
  
  .umb-grid ul li a:hover,.umb-grid ol li a:hover {
    text-decoration: underline;
  }


  .umb-grid .tl-grid-call-to-action-box {
    margin: 4rem 0 6rem;
  }
  
  @media only screen and (min-width: 768px) {
    .umb-grid .tl-grid-call-to-action-box {
      margin: 4rem 0 8rem;
    }
  }
  
  .umb-grid .tl-grid-call-to-action-box h2 {
    margin: 0 0 2rem;
  }
  
  @media only screen and (min-width: 768px) {
    .umb-grid .tl-grid-call-to-action-box h2 {
      margin-bottom: 1rem;
    }
  }
  
  .umb-grid .tl-grid-call-to-action-box p {
    margin: 0 0 2rem;
  }

  .umb-grid .full-image-wrap,.umb-grid .umb-embed {
    margin: 8rem -2rem 2rem -2rem;
    position: relative;
  }
  
  @media only screen and (min-width: 1250px) {
    .umb-grid .full-image-wrap,.umb-grid .umb-embed {
      margin: 8rem 0 2rem 0;
    }
  }


.umb-grid .about-flex {
  display:flex;
  flex-wrap:wrap;
  flex-direction:row;
  justify-content:flex-start;
  align-items:stretch;
  margin-bottom: 5.0rem;

  .about-left {
    order:1; 
    flex-basis:100%;
  }

  .about-right {
    order:2; 
    flex-basis:100%;
  }

}
@media screen and (min-width:750px) {
  .umb-grid .about-flex {
      flex-wrap:nowrap;

    .about-left {
        flex-grow: 2;
        flex-basis:600px;
        order:1;
      }

    .about-right {
      flex-grow: 2;
      flex-basis:300px;
      order:2;
      padding-top: 2.8rem
    }
  }
}

.umb-grid .text-container {
  padding: 3.0rem;
  margin: 0.1rem;
  background-color: $light-pink;
  border-radius: 1.6rem;

  & .white-background{
    background-color: $white;
    padding: 2.5rem;
    border-radius: 10px;
  }
}



  