#affixed-right-sidebar {
  h3 {
    margin: 0;
    margin-top: 16px;
    font-size: 1.6rem;
    @media screen and (min-width: 768px), print {
        margin-top: 0;
    }
  }
  ul {
    margin: 10px 0 0;
    padding: 0;
    list-style-type: none;
    li {
      list-style: none;
      a {
        text-decoration: none;
        &:hover {
          text-decoration: underline;
        }
      }
    }
  }
  li {
    border-left: 3px solid transparent;
    padding: 2px 0;
    padding-left: 4px;
    margin: 4px 0;
    line-height: 1.3;
    font-size: 1.2rem;
    &.selected {
      font-weight: 600;
      border-color: $dark-pink;
      a, a:visited{
        color: $dark-pink;
      }
    }
  }
  a, a:visited {
    color: $black;
  }

}
  

#right-sidebar-title {
  margin-right: 0.15rem;
  transition: 0.15s;

  .right-sidebar-heading{
    font-weight: 600;
    margin-left: 0.2rem;

    &:hover{
      color: $dark-pink;
    }
  }
  
  &:hover {
    color:$black;
    background-color:$snowwhite;
    -webkit-box-shadow:2px 2px 2px 2px $shadow-color;
    box-shadow:1.5px 1.5px 1.5px 1.5px $shadow-color;
    -webkit-transform:translateY(0px);
    transform:translateY(0px);
    border-radius: 3px;
    border: 0.8px solid $dark-pink;
    padding-left: 0.45rem;
    }
}

.fa-file-alt {
  color:$dark-pink;
}
