/* JOB PROFILE HERO */

  .job-profiles-hero {
    color: $black;
    margin: 0 -2rem;
    padding: 3rem 2rem;
    position: relative;
  }
  
  @media only screen and (min-width: 992px) {
    .job-profiles-hero {
      padding: 5rem 4rem 6rem;
      margin: 0;
    }
  }
  
  @media only screen and (min-width: 1920px) {
    .job-profiles-hero {
      padding: 8rem 14rem 9rem;
    }
  }
  
  .job-profiles-hero--film-and-tv-drama {
    background: $green;
  }
  
  .job-profiles-hero--visual-effects-vfx {
    background: $yellow-orange;
  }
  
  .job-profiles-hero--games {
    background: $violet;
    color: $white;
  }
  
  .job-profiles-hero--animation {
    background: $light-green;
  }
  
  .job-profiles-hero--unscripted-tv {
    background: $light-blue;
  }
  
  .job-profiles-hero__title {
    font-size: 3.2rem;
    margin-bottom: 2rem;
    font-weight: 900;
    position: relative;
    z-index: 2;
  }
  
  @media only screen and (min-width: 992px) {
    .job-profiles-hero__title {
      font-size: 4.2rem;
    }
  }
  
  @media only screen and (min-width: 1250px) {
    .job-profiles-hero__title {
      margin-bottom: 2rem;
    }
  }
  
  @media only screen and (min-width: 1920px) {
    .job-profiles-hero__title {
      font-size: 5.2rem;
    }
  }
  
  .job-profiles-hero__synopsis {
    color: $black;

    a:hover{
        text-decoration: underline;
    }
  }
  
  @media only screen and (min-width: 1250px) {
    .job-profiles-hero__synopsis {
      margin-bottom: 4rem;
    }
  }
  
  .job-profiles-hero__synopsis p {
    color: $black;
    font-size: 1.4rem;
    position: relative;
    z-index: 2;
    line-height: 1.8;
    margin-bottom: 2rem;
  }
  
  @media only screen and (min-width: 1250px) {
    .job-profiles-hero__synopsis p {
      font-size: 1.6rem;
    }
  }
  
  .job-profiles-hero__synopsis a {
    color: $black;
    font-weight: 600;
  }
  
  .job-profiles-hero--games .job-profiles-hero__synopsis p,.job-profiles-hero--games .job-profiles-hero__synopsis a {
    color: $white;
  }
  
  .job-profiles-hero__img {
    display: none;
    max-width: 100%;
    height: auto;
  }
  
  @media only screen and (min-width: 992px) {
    .job-profiles-hero__img {
      display: block;
      position: absolute;
      bottom: 0;
      right: 0;
      width: 35%;
    }
  }
  
  @media only screen and (min-width: 1650px) {
    .job-profiles-hero__img {
      width: 35%;
    }
  }