/* CONTENT */

#content-column {
  position: relative;
}

.content-wrap{

  background: $white;

  .main {
    display: block;
    margin-top: 0rem;
    margin-bottom: 4rem;
    padding-top: 2.5rem;
    padding-bottom: 3rem;
  }
  
  @media only screen and (min-width: 1250px) {
    .main {
      padding-top: 2rem;
      padding-bottom: 3.5rem;
    }
  }

  .main--no-top-padding {
    padding-top: 6rem;
  }
  
  @media only screen and (min-width: 1250px) {
    .main--no-top-padding {
      padding-top: 0;
    }
  }
  
  .main--no-bottom-padding {
    padding-bottom: 0;
  }

  .colour-splash {
    background: -moz-linear-gradient(top,transparent 70%,$dark-pink 10%);
    background: -webkit-linear-gradient(top,transparent 70%,$dark-pink 10%);
    background: linear-gradient(to bottom,transparent 70%,$dark-pink 10%);
    display: inline;
  }

 
  /* CSS for Different Types of CONTENT Pages */
  @import "main/content/contentMain/umb-grid";
  @import "main/content/contentMain/text-panel";
  @import "main/content/contentMain/job-profile-panel";
  @import "main/content/contentMain/job-profile-hero";
  @import "main/content/contentMain/job-profile-nav";
  @import "main/content/contentMain/job-profile-department";
  @import "main/content/contentMain/list-panel";
  @import "main/content/contentMain/hr";
  @import "main/content/contentMain/accordian";
  @import "main/content/contentMain/article";
  @import "main/content/contentMain/buttons";
  @import "main/content/contentMain/videos-images";
  @import "main/content/contentMain/filter-search";
  @import "main/content/contentMain/imageModal";

  /* CSS for Bottom Navigation of CONTENT DOCS Pages */
  @import "main/content/contentDocs/contentDocs";
  
  /* CSS for Bottom Navigation of CONTENT Pages */
  @import "main/content/contentNavBottom/contentNavBottom";

 /* END */





