/* SLIDE OUT */

.slide-out-mask {
    background: $dark-grey-black;
    position: fixed;
    top: 0;
    right: 0;
    height: 100%;
    width: 100%;
    z-index: 99998;
    transition: all .1s;
    opacity: 0;
    height: 0;
    overflow: hidden;
  }
  
  .slide-out-mask.fade-active {
    transition: all 0.1s;
    opacity: 0.85;
    height:100%;
    width: 100%;
  }
  
  
  .slide-out {
    background: $dark-pink;
    bottom: 0;
    overflow-x: auto;
    padding: 6rem 2rem 2rem;
    position: fixed;
    right: 0;
    top: 0;
    width: 28rem;
    z-index: 99999;
    transform: translateX(100%);
    transition: transform 500ms ease-in-out;
  }
  
  @media only screen and (min-width: 768px) {
    .slide-out {
      padding: 8.5rem 4rem 2rem;
      width: 60rem;
    }
  }
  
  @media only screen and (min-width: 992px) {
    .slide-out {
      padding-top: 7rem;
    }
  }
  
  @media only screen and (min-width: 1250px) {
    .slide-out {
      padding: 10.5rem 8rem 2rem;
      width: 80rem;
    }
  }
  
  @media only screen and (min-width: 1025px) and (max-height: 1000px) {
    .slide-out {
      padding-top: 5rem;
    }
  }
  
  @media only screen and (min-width: 1920px) {
    .slide-out {
      width: 100rem;
    }
  }
  
  .nav-is-open.slide-out {
    transform: translateX(0);
  }

  .slide-out-nav {
    margin-bottom: 4rem;
  }
  
  @media only screen and (min-width: 768px) {
    .slide-out-nav {
      margin-bottom: 6rem;
    }
  }

  .slide-out-nav__list {
    list-style: none;
    margin: 0;
    padding: 0;
  }

  .slide-out-nav__item {
    font-size: 1.4rem;
    font-weight: 700;
    line-height: 1;
    margin-bottom: 0;
    padding: 1rem 4rem 0 0;
    position: relative;
  }
  
  @media only screen and (min-width: 768px) {
    .slide-out-nav__item {
      font-size: 3.2rem;
      margin-bottom: 1.5rem;
      padding: 0;
    }
  }
  
  @media only screen and (min-width: 992px) {
    .slide-out-nav__item {
      font-size: 2.8rem;
    }
  }
  
  @media only screen and (min-width: 1250px) {
    .slide-out-nav__item {
      font-size: 4.2rem;
      margin-bottom: 1.5rem;
    }
  }
  
  @media only screen and (min-width: 1025px) and (max-height: 1000px) {
    .slide-out-nav__item {
      font-size: 2.8rem;
    }
  }

  .slide-out-nav__item:last-child {
    margin-bottom: 0;
  }
  
  .slide-out-nav__item--current .slide-out-nav__link {
    border-bottom: 0.2rem solid $white;
  }

  @media only screen and (min-width: 768px) {
    .slide-out-nav__item--current .slide-out-nav__link {
      border-bottom-width: 0.3rem;
    }
  }

  .slide-out-nav__link {
    border-bottom: 0.2rem solid transparent;
    color: $white;
    display: inline-block;
    padding-bottom: 0.3rem;
  }
  
  @media only screen and (min-width: 768px) {
    .slide-out-nav__link {
      border-bottom-width: 0.3rem;
    }
  }
  
  .slide-out-nav__link:hover {
    border-bottom-color: $white;
    text-decoration: none;
  }


  .slide-out-account-nav {
    list-style: none;
    margin: 0 0 3rem;
    padding: 0;
  }
  
  @media only screen and (min-width: 768px) {
    .slide-out-account-nav {
      margin-bottom: 5rem;
    }
  }
  
  @media only screen and (min-width: 1250px) {
    .slide-out-account-nav {
      margin-bottom: 7rem;
    }
  }
  
  @media only screen and (min-width: 1025px) and (max-height: 1000px) {
    .slide-out-account-nav {
      margin-bottom: 5rem;
    }
  }
  
  @media only screen and (min-width: 1650px) {
    .slide-out-account-nav {
      margin-bottom: 10rem;
    }
  }
  
  .slide-out-account-nav__item {
    color: $white;
    font-size: 1.4rem;
    font-weight: 700;
    line-height: 1;
    margin-bottom: 1.2rem;
  }
  
  @media only screen and (min-width: 768px) {
    .slide-out-account-nav__item {
      font-size: 1.8rem;
      margin-bottom: 1.5rem;
    }
  }
  
  @media only screen and (min-width: 992px) {
    .slide-out-account-nav__item {
      font-size: 1.4rem;
      margin-bottom: 1rem;
    }
  }
  
  @media only screen and (min-width: 1250px) {
    .slide-out-account-nav__item {
      font-size: 1.8rem;
      margin-bottom: 1.5rem;
    }
  }
  
  @media only screen and (min-width: 1025px) and (max-height: 1000px) {
    .slide-out-account-nav__item {
      font-size: 1.6rem;
      margin-bottom: 1rem;
    }
  }
  
  .slide-out-account-nav__item:last-child {
    margin-bottom: 0;
  }

  .slide-out-account-nav__link {
    color: $white;
  }
  
  .slide-out-account-nav__link:hover {
    border-bottom: 0.2rem solid $white;
    text-decoration: none;
  }

  .slide-out-social {
    font-size: 0;
    list-style: none;
    margin: 0;
    padding: 0;
  }
  
  .slide-out-social__item {
    color: $white;
    display: inline-block;
    font-size: 1.6rem;
    line-height: 1;
    margin-right: 2rem;
  }
  
  @media only screen and (min-width: 768px) {
    .slide-out-social__item {
      font-size: 2.2rem;
      margin-right: 3.5rem;
    }
  }
  
  .slide-out-social__item:last-child {
    margin-bottom: 0;
  }

  .slide-out-social__link {
    color: $white;
  }
  
  .slide-out-social__link:hover {
    color: $black;
    text-decoration: none;
  }

  .nav-dropdown-toggle {
    color: $white;
    display: block;
    font-size: 1.8rem;
    padding-top: 1rem;
    position: absolute;
    right: 0;
    height: 3.4rem;
    text-align: center;
    top: 0;
    width: 3.4rem;
    z-index: 1;
  }
  
  @media only screen and (min-width: 768px) {
    .nav-dropdown-toggle {
      font-size: 2.4rem;
      height: 3.2rem;
      padding-top: 0.5rem;
      width: 3.2rem;
    }
  }
  
  @media only screen and (min-width: 992px) {
    .nav-dropdown-toggle {
      font-size: 1.8rem;
      height: 2.8rem;
      width: 2.8rem;
    }
  }
  
  @media only screen and (min-width: 1250px) {
    .nav-dropdown-toggle {
      display: none !important;
    }
  }
  
  .nav-dropdown-toggle svg {
    -webkit-transition: transform 0.15s ease;
    -moz-transition: transform 0.15s ease;
    transition: transform 0.15s ease;
  }
  
  .nav-dropdown-toggle--is-open svg {
    -ms-transform: rotate(180deg);
    -webkit-transform: rotate(180deg);
    transform: rotate(180deg);
  }

  .mobile-nav-dropdown {
    display: none;
    list-style: none;
    margin: 0;
    padding: 0 0 0 1rem;
  }
  
  @media only screen and (min-width: 1250px) {
    .mobile-nav-dropdown {
      display: none !important;
    }
  }
  
  .mobile-nav-dropdown--is-open {
    display: block;
  }
  
  .mobile-nav-dropdown__item {
    font-size: 1.3rem;
    font-weight: 600;
    line-height: 1.4;
    margin: 0;
    padding: 0 0 1rem;
  }
  
  @media only screen and (min-width: 768px) {
    .mobile-nav-dropdown__item {
      font-size: 1.8rem;
    }
  }
  
  .mobile-nav-dropdown__item:first-child {
    margin-top: 1rem;
  }
  
  .mobile-nav-dropdown__link {
    border-bottom: 0.2rem solid transparent;
    color: $white;
    display: inline-block;
    line-height: 1;
    padding: 0 0 0.1rem;
    text-decoration: none;
  }

  .mobile-nav-dropdown__item--current .mobile-nav-dropdown__link,.mobile-nav-dropdown__link:hover {
    border-bottom-color: $white;
    text-decoration: none;
  }
  
  .mobile-nav-dropdown__item:first-child .mobile-nav-dropdown__link {
    padding-top: 1rem;
  }