/* FEEDBACK */

.site-feedback {
    position: relative;
    background: $dark-pink;
    display: block;
    color: $white;
    padding: 3rem 0;
    width: 100%;
    z-index: 1;
  }
  
  @media only screen and (min-width: 1250px) {
    .site-feedback {
      padding-top: 4rem;
      padding-bottom: 4rem;
    }
  }
  
  @media only screen and (min-width: 768px) {
    .site-feedback .container {
      padding-right: 25rem;
    }
  }
  
  @media only screen and (min-width: 1250px) {
    .site-feedback .container {
      padding-right: 2rem;
    }
  }

  .feedback-panel__title {
    color: $white;
    font-size: 2.2rem;
    font-weight: 900;
    line-height: 1.2;
    margin-bottom: 1rem;
  }
  
  @media only screen and (min-width: 768px) {
    .feedback-panel__title {
      font-size: 2.6rem;
    }
  }
  
  @media only screen and (min-width: 992px) {
    .feedback-panel__title {
      font-size: 3rem;
      line-height: 1;
    }
  }
  
  @media only screen and (min-width: 1250px) {
    .feedback-panel__title {
      font-size: 3.6rem;
    }
  }

  .feedback-panel__body {
    color: $white;
    font-weight: 500;
  }
  
  @media only screen and (min-width: 768px) {
    .feedback-panel__body {
      margin-bottom: 0;
      font-weight: 500;
    }
  }

  .feedback-buttons__container{
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .feedback-submit__btn {
    border: 0.3rem solid $white;
    color: $white;
    padding: 0;
    width: 100%;
    font-weight: 600;
    display: flex;
    justify-content: center;
    align-items: center;
    line-height: 4.0rem;
    margin-bottom: 1.5rem
  }

.feedback-view__btn  {
    border: 0.3rem solid $white;
    color: $white;
    padding: 0;
    width: 100%;
    font-weight: 600;
    display: flex;
    justify-content: center;
    align-items: center;
    line-height: 4.0rem;
  }
  
  @media only screen and (min-width: 768px) {
    .feedback-submit__btn {
    position: absolute;
    top: 50%;
    -webkit-transform: translateY(-50%);
    -ms-transform: translateY(-50%);
    transform: translateY(-50%);
    right: 19rem;
    width: 15.5rem;
    line-height: 4.0rem;
    }
  }

  @media only screen and (min-width: 768px) {
    .feedback-view__btn {
    position: absolute;
    top: 50%;
    -webkit-transform: translateY(-50%);
    -ms-transform: translateY(-50%);
    transform: translateY(-50%);
    right: 2rem;
    width: 15.5rem;
    line-height: 4.0rem;
    }
  }
  
  @media only screen and (min-width: 1250px) {
    .feedback-submit__btn {
    height: 5rem;
    right: 22rem;
    width: 18rem;
    }
  }

  @media only screen and (min-width: 1250px) {
    .feedback-view__btn {
    height: 5rem;
    width: 18rem;
    }
  }
  
  .feedback-submit__btn:hover, .feedback-view__btn:hover {
    background: $white;
    color: $dark-pink;
  }