/* COMMON CSS */


h1,h2,h3,h4,h5,h6,p,.validation-summary-errors,.field-validation-error {
  margin: 0;
}

ul li, ol li, .li-list li, .ol-list li, ul__item, ol__item, .li-list__item, .ol-list__item {
	color: $black;
	font-weight: 400;
	line-height: 1.8;
	margin: 0;
	margin-bottom: 1rem;
	font-size: 1.6rem;
	padding: 0;
}

@media only screen and (min-width: 768px) {
  ul li,ol li,.li-list li,.ol-list li,ul__item,ol__item,.li-list__item,.ol-list__item {
    font-size: 1.8rem;
  }
}

p, .validation-summary-errors, .field-validation-error {
	margin-bottom: 2rem;
}


.container {
  margin-right: auto;
  margin-left: auto;
  padding-left: 2rem;
  padding-right: 2rem;
  position: relative;
}

@media only screen and (min-width: 992px) {
  .container {
    max-width: 115rem;
  }
}

@media only screen and (min-width: 1250px) {
  .container {
    max-width: 125rem;
  }
}

@media only screen and (min-width: 1440px) {
  .container {
    max-width: 134rem;
  }
}

@media only screen and (min-width: 1650px) {
  .container {
    max-width: 144rem;
  }
}

@media only screen and (min-width: 1920px) {
  .container {
    max-width: 170rem;
  }
}

.container:before,.container:after {
  display: table;
  content: " ";
}

.container:after {
  clear: both;
}

.c:before,.c:after {
  content: " ";
  display: table;
}

.c:after {
  clear: both;
}

.c {
  *zoom: 1;
}

/* VISIBILITY */

