/* GRIDS */

@supports (display: grid) {
  .grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(290px, 1fr));
    grid-column-gap: 1rem;
    row-gap: 1rem;
    margin: 0;
    padding: 0;
  }
  .grid > .grid-item {
    display: flex;
    align-content: stretch;
    align-items: stretch;
    transition: all 200ms linear;
  }

  .grid > .grid-item:hover {
      background-color: $white;
      transition: all 250ms linear;;
      -webkit-box-shadow:0 10px 40px 0 $shadow-color;
      box-shadow:0 10px 40px 0 $shadow-color;
  }

  .grid > .grid-item > * {
    width: 100%;
    height: 100%;
  }


  .grid:not(.is-horizontal) > .grid-item:only-child {
    max-width: 308px;
  }

}

@media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
  .grid {
    display: flex;
    flex-wrap: wrap;
    width: 100%;
    list-style: none;
  }

  .grid > .grid-item {
    width: inherit;
    min-width: 256px;
    max-width: 308px;
    display: flex;
    align-content: stretch;
    align-items: stretch;
    margin-bottom: 1rem;
    margin-right: 1rem;
  }
  .grid > .grid-item > * {
    width: 100%;
    height: 100%;
  }

}


/* COLUMNS */

  .columns {

    &:last-child{
      margin-bottom: -0.75rem;
    }

    &:not(:last-child){
      margin-bottom: calc(1.5rem - 0.75rem);
    }

    &.is-gapless {
      margin-left: 0;
      margin-right: 0;
      margin-top: 0;
    }

    &.is-gapless > .column {
      margin: 0 !important;
      padding: 0 !important;
    }

    &.is-mobile {
      display: flex;
    }

    &.is-mobile > .column.is-narrow {
      flex: none;
    }

    &.has-large-gaps{
      margin-left: -2.4rem;
      margin-right: -2.4rem;
      margin-top: -1.6rem;
    }

    &.has-large-gaps:last-child {
      margin-bottom: -1.5rem;
    }

    &.has-large-gaps:not(:last-child) {
      margin-bottom: calc(1.5rem - 1.5rem);
    }

    &.has-large-gaps > .column {
      padding: 2.4rem !important;
      padding-top: 1.6rem !important;
    }

    @media screen and (min-width: 768px), print {
      &:not(.is-desktop) {
        display: flex;
      }
    }

    @media screen and (min-width: 1088px) {
      .column.is-desktop {
        display: flex;
      }
    }

    @media screen and (max-width: 767px), screen and (min-resolution: 120dpi) and (max-width: 767.9px) {
      &.is-gapless-mobile {
        margin-left: 0 !important;
        margin-right: 0 !important;
        margin-top: 0 !important;

        & > .column {
          margin: 0;
          padding: 0;
        }

        &:not(:last-child) {
          margin-bottom: 1.6rem;
        }
        &:last-child {
          margin-bottom: 0;
        }
      }
    }

  }



/* COLUMN */

  .column {
