    flex-basis: 0;
    flex-grow: 1;
    flex-shrink: 1;
    padding: 1.5rem !important;

    @media screen and (min-width: 768px), print {

      &.is-one-third-tablet {
        flex: none;
        width: 33.3333%;
      }
      &.is-one-quarter-tablet {
        flex: none;
        width: 25%;
      }
      &.is-one-fifth-tablet {
        flex: none;
        width: 20%;
      }
      &.is-two-thirds-tablet {
        flex: none;
        width: 66.6666%;
      }
      &.is-three-quarters-tablet {
        flex: none;
        width: 75%;
      }
      &.is-four-fifths-tablet {
        flex: none;
        width: 80%;
      }
      &.is-full {
        flex: none;
        width: 100%;
      }
      &.is-one-quarter {
        flex: none;
        width: 25%;
      }
      &.is-9, &.is-9-tablet {
