        width: 75%;
      }
      &.is-6, &.is-6-tablet {
        flex: none;
        width: 50%;
      }
      &.is-4, &.is-4-tablet {
        flex: none;
        width: 33.33333%;
      }
      &.is-two-fifths, &.is-two-fifths-tablet {
        flex: none;
        width: 40%;
      }
      &.is-three-fifths, &.is-three-fifths-tablet {
        flex: none;
        width: 60%;
      }
    }

    @media screen and (min-width: 1088px) {

      &.is-one-quarter-desktop {
        flex: none;
        width: 25%;
      }
      &.is-three-quarters-desktop {
        flex: none;
        width: 75%;
      }
      &.is-four-fifths-desktop {
        flex: none;
        width: 80%;
      }
      &.is-five-sixths-desktop {
        flex: none;
        width: 83.33333%;
      }
      &.is-six-sevenths-desktop {
        flex: none;
        width: 85.71%;
      }
      &.is-one-fifth-desktop {
        flex: none;
        width: 20%;
      }
      &.is-one-sixth-desktop {
        flex: none;
        width: 16.66666%;
      }
      &.is-one-seventh-desktop {
        flex: none;
        width: 14.29%;
      }
      &.is-10-desktop {
        flex: none;
        width: 83.33333%;
      }
      &.is-4-desktop {
        flex: none;
        width: 33.33333%;
      }
      &.is-3-desktop {
        flex: none;
        width: 25%;
      }
    }

    @media screen and (min-width: 1088px) {

      &.is-one-sixth-widescreen {
        flex: none;
        width: 16%;
      }

      &.is-one-fifth-widescreen {
        flex: none;
        width: 20%;
      }

      &.is-one-quarter-widescreen {
        flex: none;
        width: 25%;
      }

      &.is-three-quarters-widescreen {
        flex: none;
        width: 75%;
      }

      &.is-five-sixths-widescreen {
        flex: none;
        width: 84%;
      }

    }

  }


  /* ICONS */

  .material-icons {
    font-family: 'Material Icons';
    font-weight: normal;
    font-style: normal;
    font-size: 24px;
    line-height: 1;
    letter-spacing: normal;
    text-transform: none;
    display: inline-block;
    white-space: nowrap;
    word-wrap: normal;
    direction: ltr;
    -moz-font-feature-settings: 'liga';
    -moz-osx-font-smoothing: grayscale;
  }

  .icon-is-medium {
    display: inline-flex;
    justify-content: center;
    align-items: center;
    height: 2.5rem;
    width: 2.5rem;
    margin-right: 1.0rem;
  }
  
  .icon--m {
    width: 1.6rem;
    height: 1.6rem;
  }
  
  @media only screen and (min-width: 768px) {
    .icon--m {
      width: 2.4rem;
      height: 2.4rem;
    }
  }


  /* IMAGES */

  img, video {
    height: auto;
    max-width: 100%;
  }


  /* CARDS */

  .card {
    outline-color: $body-color;
    color: $body-color;
    max-width: 100%;
    position: relative;
    padding: 2.5rem 0 0;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    border-radius: 0.25rem;
    border: 1px solid transparent;
    background-color: transparent;
  
    -webkit-animation: fadein 1s; /* Safari, Chrome and Opera > 12.1 */
    -moz-animation: fadein 1s; /* Firefox < 16 */
    -ms-animation: fadein 1s; /* Internet Explorer */
    -o-animation: fadein 1s; /* Opera < 12.1 */
    animation: fadein 1s;
  
    @keyframes fadein {
      from { opacity: 0; }
      to   { opacity: 1; }
    }
  
    /* Firefox < 16 */
    @-moz-keyframes fadein {
      from { opacity: 0; }
      to   { opacity: 1; }
    }
  
    /* Safari, Chrome and Opera > 12.1 */
    @-webkit-keyframes fadein {
      from { opacity: 0; }
      to   { opacity: 1; }
    }
  
    /* Internet Explorer */
    @-ms-keyframes fadein {
      from { opacity: 0; }
      to   { opacity: 1; }
    }
  
    /* Opera < 12.1 */
    @-o-keyframes fadein {
      from { opacity: 0; }
      to   { opacity: 1; }
    }
  
    & > :first-child {
      border-top-right-radius: 0.25rem;
      border-top-left-radius: 0.25rem;
    }
    & > :last-child {
      border-bottom-right-radius: 0.25rem;
      border-bottom-left-radius: 0.25rem;
    }
    &.is-uniform {
      padding: 1.5rem !important;
    }
  
    .card-header,
    .card-content,
    .card-footer {
    padding-left: 2.5rem;
    padding-right: 2.5rem;
    }
  
    .card-header:first-child,
    .card-content:first-child,
    .card-footer:first-child {
    padding-top: 2.5rem;
    margin-top: -2.5rem;
    }
  
    .card-content {
      flex-grow: 1;
      margin-bottom: 1.25rem;
    }
  
    .card-content > *:not(:first-child) {
      margin-top: 0.5rem;
    }
  
    .card-content .card-content-super-title {
      letter-spacing: 0.225rem;
      line-height: 1;
      text-transform: uppercase;
      font-size: 0.75rem;
    }
  
    .card-content .card-content-title {
      font-size: 2.0rem;
      font-family: 'Raleway', sans-serif;
      flex-grow: 1;
      font-weight: 600;
      line-height: 1.25;
      text-align: left;
      -webkit-line-clamp: 3;
      color: $black;
      
      & h3 {
        font-weight: 700 !important;
      }

      &:hover{
        text-decoration: underline;
      }

    }
  
    .card-content .card-content-title:first-child {
      margin-top: none;
    }
  
    .card-content .card-content-title:only-child {
      margin: 0;
    }
   
  
    .card-content ul.card-content-metadata {
      color: $dategrey;
      line-height: 0.875rem;
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
      list-style: none;
      margin-bottom: 0;
      margin-right: 0;
      margin-left: 0;
      padding: 0;
    }
  
    .card-content ul.card-content-metadata > li {
      list-style: none;
      display: flex;
      flex-basis: auto;
      margin-bottom: 0.25rem;
    }
  
    .card-content ul.card-content-metadata > li:not(:only-child):not(:last-child) {
      margin-right: 0.75rem;
    }
  
  
    .card-content ul.card-content-metadata > li:first-of-type, .card-content ul.card-content-metadata > li:only-of-type {
      margin-left: 0;
    }
  
    @media screen and (max-width: 767px), screen and (min-resolution: 120dpi) and (max-width: 767.9px) {
      .card-content ul.card-content-metadata > li:last-of-type {
        margin-left: 0;
      }
    }
  
    .card-content ul.card-content-metadata > li > time {
      font-size: 1.4rem;
    }
  
    .card-content p.card-content-description {
      line-height: 1.25;
      font-size: 1.6rem;
      font-family: 'Raleway', sans-serif;
      font-weight: 450;
      line-height: 1.6;
    }
  
    @supports (display: grid) {
     .card-content p.card-content-description {
        -webkit-line-clamp: 4;
        max-height: inherit !important;
        padding-bottom: 0 !important;
        font-family: 'Raleway', sans-serif;
        font-weight: 450;
      }
     .card-content p.card-content-description:after {
        display: none;
      }
    }
  
    @media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
      .card-content p.card-content-description {
        position: relative;
        overflow: hidden;
        max-height: 4rem;
        padding-bottom: 1rem;
        font-family: 'Raleway', sans-serif;
        font-weight: 450;
      }
      .card-content p.card-content-description:after {
        position: absolute;
        z-index: 2;
        bottom: -1px;
        right: 0;
        left: 0;
        height: 1rem;
        content: '';
      }
    }
  
  }


/* TAGS */

