  }

}


[class^='tag-'] {
  .delete
    {
      margin-left: 0.25rem;
      margin-right: -0.375rem;
    }
    
    .delete {
      color: inherit;
      -moz-appearance: none;
      -webkit-appearance: none;
      background-color: transparent;
      border: none;
      cursor: pointer;
      display: inline-block;
      flex-grow: 0;
      flex-shrink: 0;
      font-size: 0;
      height: 15px;
      max-height: 15px;
      max-width: 15px;
      min-height: 15px;
      min-width: 15px;
      outline: none;
      position: relative;
      vertical-align: top;
      width: 15px;
    }
    
    .delete:before, .delete:after {
      background-color: currentColor;
      content: '';
      display: block;
      left: 50%;
      position: absolute;
      top: 50%;
      transform: translateX(-50%) translateY(-50%) rotate(45deg);
      transform-origin: center center;
      border: 1px solid transparent;
    }
    
    .delete:before {
      width: 50%;
    }
    
    .delete:after {
      height: 50%;
    }
    
    .delete:hover:before .delete:hover:after {
      border-color: currentColor;
    }
    
    .delete:active:before, .delete:active:after {
      border-color: $darkgrey;
    }
}

[class^='tag-'] {
  .is-delete:hover, is-delete:active, is-delete.focus-visible
    {
      opacity: 95%;
    }
}

.tag-{ 

  &filterOne .delete:before, &filterOne .delete:after {
    border-color: transparent;
  }

  &filterTwo .delete:before, &filterThree .delete:after {
    border-color: transparent;
  }

  &filterThree .delete:before, &filterThree .delete:after {
    border-color: transparent;
  }

}


