/* JOB PROFILE DEPARTMENT */

.job-profiles-department-grid-content {
    display: none;
  }
  
  .job-profiles-department-wrap {
    padding: 0 2rem 3rem;
    margin: 0 -2rem;
  }
  
  .job-profiles-department-wrap--film-and-tv-drama {
    background: $green;
  }
  
  .job-profiles-department-wrap--visual-effects-vfx {
    background: $yellow-orange;
  }
  
  .job-profiles-department-wrap--games {
    background: $violet;
  }
  
  .job-profiles-department-wrap--animation {
    background: $light-green
  }
  
  .job-profiles-department-wrap--unscripted-tv {
    background: $light-blue
  }
  
  @media only screen and (min-width: 992px) {
    .job-profiles-department-wrap {
      background: none;
      padding: 0;
      margin: 0 0 4rem 0;
    }
  }
  
  .job-profiles-department {
    background: $white;
    border: 0.3rem solid $black;
    box-shadow: -0.4rem 0.4rem 0 $black;
    margin-bottom: 1rem;
  }
  
  @media only screen and (min-width: 992px) {
    .job-profiles-department {
      border: 0;
      box-shadow: none;
      margin-bottom: 0;
      padding: 4rem 0 2rem 0;
    }
  
    .job-profiles-department:nth-child(2n) {
      background: $navgrey;
    }
  }
  
  @media only screen and (min-width: 1250px) {
    .job-profiles-department {
      padding: 6rem 0 4rem 0;
    }
  }
  
  @media only screen and (min-width: 992px) {
    .job-profiles-department__inner {
      display: flex;
      padding-left: 4rem;
      padding-right: 4rem;
    }
  }
  
  @media only screen and (min-width: 1920px) {
    .job-profiles-department__inner {
      padding-left: 14rem;
      padding-right: 14rem;
    }
  }
  
  .job-profiles-department__content {
    display: none;
    padding: 0.5rem 1.5rem;
  }
  
  .is-open .job-profiles-department__content {
    display: block;
  }
  
  @media only screen and (min-width: 992px) {
    .job-profiles-department__content {
      padding: 0;
      display: block;
    }
  }
  
  .job-profiles-department__title {
    font-size: 1.8rem;
    line-height: 1.2;
    font-weight: 700;
    margin-bottom: 0.8rem;
    position: relative;
    padding: 1.1rem 3rem 0 1.5rem;
    cursor: pointer;
  }
  
  @media only screen and (min-width: 992px) {
    .job-profiles-department__title {
      font-size: 2.4rem;
      font-weight: 800;
      padding: 0;
      cursor: default;
    }
  }
  
  @media only screen and (min-width: 1250px) {
    .job-profiles-department__title {
      font-size: 3.2rem;
      margin-bottom: 1rem;
    }
  }
  
  .job-profiles-department__title-suffix {
    display: none;
  }
  
  @media only screen and (min-width: 768px) {
    .job-profiles-department__title-suffix {
      display: inline;
    }
  }
  
  .job-profiles-department__plus-minus {
    position: absolute;
    right: 1.4rem;
    top: 50%;
    margin-top: -0.5rem;
    font-size: 2.1rem;
    height: 1.8rem;
    width: 1.8rem;
  }
  
  @media only screen and (min-width: 992px) {
    .job-profiles-department__plus-minus {
      display: none;
    }
  }
  
  .job-profiles-department__plus-minus:before,.job-profiles-department__plus-minus:after {
    content: "";
    position: absolute;
    background-color: $black;
    transition: transform 0.25s ease-out;
  }
  
  .job-profiles-department__plus-minus:before {
    top: 0;
    left: 50%;
    width: 4px;
    height: 100%;
    margin-left: -2px;
  }
  
  .job-profiles-department__plus-minus:after {
    top: 50%;
    left: 0;
    width: 100%;
    height: 4px;
    margin-top: -2px;
  }
  
  .is-open .job-profiles-department__plus-minus {
    cursor: pointer;
  }
  
  .is-open .job-profiles-department__plus-minus:before {
    transform: rotate(90deg);
  }
  
  .is-open .job-profiles-department__plus-minus:after {
    transform: rotate(180deg);
  }
  
  .job-profiles-department__synopsis {
    display: none;
    font-size: 1.4rem;
  }
  
  @media only screen and (min-width: 992px) {
    .job-profiles-department__synopsis {
      display: block;
    }
  }
  
  @media only screen and (min-width: 1250px) {
    .job-profiles-department__synopsis {
      font-size: 1.6rem;
    }
  }
  
  @media only screen and (min-width: 992px) {
    .job-profiles-department__synopsis-mobile {
      display: none;
    }
  }
  
  .job-profiles-department__jobs {
    margin: 0 0 1rem 0;
    padding: 0;
    border-top: 1px solid $ligher-grey;
  }
  
  @media only screen and (min-width: 992px) {
    .job-profiles-department__jobs {
      border: 0;
      margin: 0 -1rem;
      display: flex;
      flex-wrap: wrap;
    }
  }
  
  .job-profiles-department__job {
    list-style-type: none;
    margin: 0;
    position: relative;
  }
  
  @media only screen and (min-width: 992px) {
    .job-profiles-department__job {
      width: 25%;
      padding-left: 1rem;
      padding-right: 1rem;
      margin-bottom: 2rem;
      transition: transform 100ms;
    }
  
    .job-profiles-department__job:hover {
      transform: scale(1.05) rotate(-2deg);
    }
  }
  
  @media only screen and (min-width: 1250px) {
    .job-profiles-department__job {
      width: 20%;
    }
  }
  
  @media only screen and (min-width: 992px) {
    .job-profiles-department__job:nth-child(even):hover {
      transform: scale(1.05) rotate(2deg);
    }
  }
  
  .job-profiles-department__job-title {
    font-size: 1.4rem;
    margin: 0;
    display: inline-block;
  }
  
  @media only screen and (min-width: 992px) {
    .job-profiles-department__job-title {
      color: $black;
      line-height: 1.3;
      margin-top: 0.4rem;
    }
  
    .job-profiles-department-wrap--games .job-profiles-department__job-title {
      color: $white;
    }
  
    .job-profiles-department__job-title--previsualisation-previs-artist {
      font-size: 1.2rem;
    }
  }
  
  @media only screen and (min-width: 1250px) {
    .job-profiles-department__job-title {
      font-size: 1.6rem;
    }
  
    .job-profiles-department__job-title--previsualisation-previs-artist {
      font-size: 1.4rem;
    }
  }
  
  @media only screen and (min-width: 1440px) {
    .job-profiles-department__job-title {
      font-size: 1.8rem;
    }
  
    .job-profiles-department__job-title--previsualisation-previs-artist {
      font-size: 1.6rem;
    }
  }
  
  @media only screen and (min-width: 1920px) {
    .job-profiles-department__job-title {
      font-size: 1.9rem;
    }
  
    .job-profiles-department__job-title--previsualisation-previs-artist {
      font-size: 1.9rem;
    }
  }
  
  .job-profiles-department__job-link {
    font-weight: 700;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    position: relative;
    border-bottom: 1px solid $ligher-grey;
    padding: 0.8rem 2rem 0.5rem 0;
  }
  
  .job-profiles-department-wrap--film-and-tv-drama .job-profiles-department__job-link {
    color: $green;
  }
  
  .job-profiles-department-wrap--visual-effects-vfx .job-profiles-department__job-link {
    color: $yellow-orange;
  }
  
  .job-profiles-department-wrap--games .job-profiles-department__job-link {
    color: $violet;
  }
  
  .job-profiles-department-wrap--animation .job-profiles-department__job-link {
    color: $light-green;
  }
  
  .job-profiles-department-wrap--unscripted-tv .job-profiles-department__job-link {
    color: $light-blue;
  }
  
  .job-profiles-department__job-link:hover {
    text-decoration: none;
  }
  
  @media only screen and (min-width: 992px) {
    .job-profiles-department__job-link {
      border: 0;
      padding: 0.5rem 1rem 1.2rem 1rem;
      border-radius: 1rem;
      height: 100%;
      text-align: center;
      transition: box-shadow 100ms;
    }
  
    .job-profiles-department__job-link:hover {
      box-shadow: 0 5rem 5rem $shadow-color;
    }
  
    .job-profiles-department-wrap--film-and-tv-drama .job-profiles-department__job-link {
      background: $green;
    }
  
    .job-profiles-department-wrap--visual-effects-vfx .job-profiles-department__job-link {
      background: $yellow-orange;
    }
  
    .job-profiles-department-wrap--games .job-profiles-department__job-link {
      background: $violet;
    }
  
    .job-profiles-department-wrap--animation .job-profiles-department__job-link {
      background: $light-green;
    }
  
    .job-profiles-department-wrap--unscripted-tv .job-profiles-department__job-link {
      background: $light-blue;
    }
  }
  
  .job-profiles-department__job-arrow {
    top: 50%;
    position: absolute;
    right: 0;
    margin-top: -1rem;
    width: 1.8rem;
  }
  
  @media only screen and (min-width: 992px) {
    .job-profiles-department__job-arrow {
      display: none;
    }
  }
  
  .job-profiles-department__img {
    display: none;
    max-width: 100%;
    height: auto;
    flex-shrink: 0;
  }
  
  @media only screen and (min-width: 992px) {
    .job-profiles-department__img {
      display: block;
      margin-bottom: -0.5rem;
    }
  }
  
  .job-profiles-department__tag {
    font-size: 1.3rem;
    font-weight: 700;
    margin: 0;
    color: $white;
    display: none;
    background: $dark-pink;
    padding: 0 0.5rem;
    position: relative;
    z-index: 10;
    cursor: pointer;
  }
  
  @media only screen and (min-width: 992px) {
    .job-profiles-department__tag {
      display: inline-block;
      position: absolute;
      top: -1rem;
      left: 50%;
      transform: translateX(-50%);
      white-space: nowrap;
    }
  
    .job-profiles-department__tag:hover {
      background: $dark-pink-dark;
    }
  }
  
  @media only screen and (min-width: 992px) {
    .job-profiles-department__intro {
      width: 35%;
      padding-right: 3rem;
    }
  }
  
  @media only screen and (min-width: 1250px) {
    .job-profiles-department__intro {
      width: 30%;
    }
  }
  
  @media only screen and (min-width: 992px) {
    .job-profiles-department__content {
      width: 65%;
    }
  }
  
  @media only screen and (min-width: 1250px) {
    .job-profiles-department__content {
      width: 70%;
    }
  }

