/* FILTER SEARCH PANEL */

.filter-search-panel-wrap {
    padding-bottom: 0rem;
}

.filter-search-panel__title {
    font-size: 2.5rem;
    font-weight: 500;
    line-height: 1.5;
    margin-bottom: 0.5rem;
}

.filter-search-panel__filter-logic {
    display:flex;
    align-items: center;
}

.filter-search-panel__switch {
    flex-grow: 0;
    flex-shrink: 0;
    flex-basis: 60px;
    margin-right: 0.5rem;
    height: 24px;
    position: relative;
    display: inline-block;    
}

.filter-search-panel__input {
    line-height: normal;
    margin: 0;
    overflow: visible;
    font-family: inherit;
    font-size: 100%;
}

.filter-search-panel__switch .filter-search-panel__input {
    opacity: 0;
    width: 0;
    height: 0;  
}

.filter-search-panel__slider {
    position: absolute;
    cursor: pointer;
    border-radius: 24px;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #ccc;
    -webkit-transition: .4s;
    transition: .4s;
}

.filter-search-panel__slider:before {
    position: absolute;
    content: "";
    height: 16px;
    width: 16px;
    left: 4px;
    bottom: 4px;
    background-color: $white;
    -webkit-transition: .4s;
    transition: .4s;
    border-radius: 50%;
}

.filter-search-panel__slider-text{
    font-size: 1.6rem;
}

.filter-search-panel__input:checked + .filter-search-panel__slider:before {
    -webkit-transform: translateX(36px);
    -ms-transform: translateX(36px);
    transform: translateX(36px);
}

.filter-search-panel__horizontal-line{
    border: 0;
    border-top: 1px solid $border-bottom;
    margin-bottom: 1rem;
    margin-top: 0.5rem;
}

.filter-search-panel__clear-item{
    padding-bottom: 0.5rem !important;
    margin-bottom: 0rem !important;
}

.filter-search-panel__clear-button{
    padding: 0;
    margin-top: 0.5rem !important;
    border: none;
    cursor: pointer;
    font-size: 1.6rem; 
    background-color: transparent;
    
}

.filter-search-panel-category{
    margin-bottom: 1rem;
    border-bottom: 1px solid $border-bottom;
}

.filter-search-panel-category__title{
    font-size: 1.8rem;
    margin-bottom: 0.5rem !important;
    word-break: break-word;
    font-weight: 500;
    line-height: 1.125;
}

.filter-search-panel-category__box{
    max-height: 330px;
    overflow-x: hidden;
    overflow-y: auto;
    -webkit-overflow-scrolling: touch;
}

.filter-search-panel-category-list{
    padding: 0 !important;
    margin: 0 !important;
    padding-bottom: 1rem !important;
}

.filter-search-panel-category-list__item{
    display: flex !important;
    margin-left: 0.5rem !important;
    margin-bottom: 0rem !important;
    padding-top: 0.5rem !important;
}

.filter-search-panel-checkbox{
    cursor: pointer;
    display: flex;
    line-height: 1.25;
    position: relative;
    align-items: center;

    & input[type='checkbox'] {
        border: 0;
        clip: rect(1px, 1px, 1px, 1px);
        clip-path: inset(50%);
        height: 1px;
        margin: -1px;
        overflow: hidden;
        padding: 0;
        position: absolute;
        width: 1px;
        word-wrap: normal;
      }

     & input {
        line-height: normal;
        margin: 0;
        overflow: visible;
        font-family: inherit;
        font-size: 100%;
    }
}

.filter-search-panel-checkbox:hover .filter-search-panel-checkbox-check:before {
    content: '\2714' !important;
}   

.filter-search-panel-checkbox-check {
    display: flex;
    width: 1.75rem;
    height: 1.75rem;
    margin-right: 0.7rem;
    justify-content: center;
    align-items: center;
    flex-shrink: 0;
    border: 1px solid $body-color;
    border-radius: 2px;
    transition-property: background, border, border-color;
    transition-duration: 200ms;
    transition-timing-function: cubic-bezier(0.4, 0, 0.23, 1);
}


.filter-search-panel-checkbox-check:before {
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 1.2rem;
    color: $secondarygrey;
}
  
.filter-search-panel-checkbox input[type='checkbox']:checked + .filter-search-panel-checkbox-check:before {
    content: '\2714' !important;
    color: $white;
}
  
.filter-search-panel-checkbox input[type='checkbox'][disabled] + .filter-search-panel-checkbox-check {
    opacity: 0.6;
    cursor: not-allowed;
}

.filter-search-panel-checkbox-text{
    font-size: 1.55rem;
    font-weight: 400;
}

.filter-search-panel-checkbox-count{
    font-size: small;
    font-style: italic;
}

/* FILTER SEARCH RESULTS */

.filter-search-results__search-area{
    padding-top: 1.0rem;
}

.filter-search-results__search-bar{
    max-width: 100%;
    width: 100%;
    height: 2.3em;
    margin: 0.0rem;
    padding-bottom: calc(0.375em - 1px);
    padding-left: calc(0.625em - 1px);
    padding-right: calc(0.625em - 1px);
    padding-top: calc(0.375em - 1px);
    overflow: visible;
    box-shadow: inset 0 1px 2px $shadow-color;
    border: 1px solid transparent;
    border-color: $dark-border-color;
    border-radius: 4px;
    line-height: 1.5;
    font-size: 1.75rem;
    appearance: none;
    background-color: $white;
    color: $black !important;   
}

.filter-search-results__search-bar:focus{
    outline: none !important;
}

.filter-search-results__search-bar:active {
    outline-style: solid !important;
    outline-offset: 0 !important;
    outline: none !important;
  }
  
  .filter-search-results__search-bar:hover {
    z-index: 2;
  }

.filter-search-results__search-button{
    display: inline-flex;
    position: relative;
    vertical-align: top;
    justify-content: center;
    text-align: center;
    white-space: nowrap;
    margin-left: 0.8rem !important;
    padding-bottom: calc(0.375em - 1px);
    padding-left: 0.5em;
    padding-right: 0.75em;
    padding-top: calc(0.375em - 1px);
    height: 4.0rem;
    text-decoration: none !important;
    box-shadow: none;
    line-height: 1.5;
    border: none;
    color: white;
    font-size: 1.75rem;
    user-select: none;
    border-radius: 2px;
    border-width: 1px;
    cursor: pointer;

    @media screen and (max-width: 767px), screen and (min-resolution: 120dpi) and (max-width: 767.9px) {
        padding-left: 0.7em;
        padding-right: 0.7em;
    }
}

.filter-search-results__search-button:hover{
    
}

.filter-search-results__search-button:active{
    
}

.filter-search-results__search-button-icon{
    margin-top: 0.2rem;
}

.filter-search-results__tagged-items-container{
    display: flex !important;
    flex-wrap: wrap !important;
    margin-top: 2.3rem !important;  
}

.filter-search-results__count-container{
    margin-top: 1.5rem !important;
}

.filter-search-results__count{
    margin-top: 1.5rem !important;
    border-bottom: $lightish-grey; 
    border-bottom-style: solid; 
    line-height: 3em; 
    border-bottom-width: thin;
    font-size: 1.5rem;
    font-weight: 600;
}

.filter-search-grid-results{
    margin-top: 1.5rem !important;
}

.filter-search-grid-results-no-result {
    margin-top: 1.5rem !important;
    margin-bottom: 1rem !important;
    text-align: center;

    figure {
            margin-bottom: 1rem !important;
        }

    h2 {
        font-size: 2.5rem;
        margin-bottom: 1rem !important;
        word-break: break-word;
        font-weight: 500;
        line-height: 1.125;
    }
}


/* PAGINATION */
.pagination-wrap {
    margin-top: 4rem;
    text-align: center;
}

.filter-search-pagenumbers{
    margin-top: 4rem;
    margin-left: 2rem;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
}

.filter-search-pagenumbers-button{
	min-width: 3.5rem;
	height: 3.5rem;
	appearance: none;
	display: inline-flex;
	position: relative;
	align-items: center;
	justify-content: center;
    text-align: center;
    font-size: 1.5rem;
	padding-left: 0.8em;
	padding-right: 0.8em;
	border-radius: 4px;
	border: 1px solid $border-color;
	outline: none;
	cursor: pointer;
	margin: 3px;
	transition: 0.2s;
	background-color: $lightgreywhite;
    &:hover{
        box-shadow: 0 7px 14px 0 $shadow-color;
        -webkit-transform:translateY(-0.5px);
        transform:translateY(-0.5px)
      }
      &.active{
        border: 1px solid transparent;
      }
}