  /* VIDEO & IMAGES */

  .hero-img {
    position: relative;
    margin: 3rem -2rem 2rem -2rem;
  }
  
  @media only screen and (min-width: 992px) {
    .hero-img {
      margin: 3rem 0 2rem 0;
    }
  }

  .hero-img__img {
    background-position: 50% 50%;
    background-repeat: no-repeat;
    background-size: cover;
    display: block;
    height: 0;
    padding-top: 56.25%;
    width: 100%;
    transition: all 400ms;

    &:hover{
      transform: scale(1.01);
      box-shadow:0 10px 40px 0 $shadow-color;
    }
  }
  
  .video-wrapper {
    position: relative;
    padding-bottom: 56.25%;
    height: 0;
  }
  
  .video-wrapper iframe {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }

  @media only screen and (min-width: 1250px) {
    .umb-grid .wrapped-inline-image {
      margin: 0 0 6rem 0;
    }
  
    .umb-grid .wrapped-inline-image .image-with-accreditation-wrapper {
      margin-left: 0;
    }
  }
  
  .umb-grid .image-with-accreditation-wrapper {
    position: relative;
  }
  
  .umb-grid .image-with-accreditation-wrapper img {
    display: block;
    height: auto;
    width: 100%;
  }
  
  .umb-grid .full-image-wrap,.umb-grid .umb-embed {
    margin: 8rem -2rem 2rem -2rem;
    position: relative;
  }
  
  @media only screen and (min-width: 1250px) {
    .umb-grid .full-image-wrap,.umb-grid .umb-embed {
      margin: 8rem 0 2rem 0;
    }
  }
  
  .umb-grid .full-image {
    width: 100%;
    height: auto;
  }
  
  .umb-grid .inline-block {
    margin: 0 0 3rem;
  }
  
  @media only screen and (min-width: 768px) {
    .umb-grid .inline-block {
      float: left;
      max-width: 33.33%;
      max-width: calc(33.33% - 0);
      margin: 0 6rem 4rem 0;
    }
  }
  
  .umb-grid .full-gallery-wrap {
    margin: 4rem -1rem;
  }
  
  .umb-grid .full-gallery-wrap .gallery-item-container {
    float: left;
    padding: 1rem 1rem 1rem 1rem;
    width: 50%;
  }
  
  @media only screen and (min-width: 768px) {
    .umb-grid .full-gallery-wrap .gallery-item-container {
      width: 33.333333%;
    }
  }
  
  .umb-grid .full-gallery-wrap .gallery-item-container .gallery-item {
    -webkit-transition: all 0.25s ease;
    -moz-transition: all 0.25s ease;
    transition: all 0.25s ease;
    background-position: 50% 50%;
    background-repeat: no-repeat;
    background-size: cover;
    display: block;
    height: 0;
    padding-bottom: 100%;
    position: relative;
    width: 100%;
  }

  .umb-grid .about-image{
    display: flex;
    justify-content: center;
    padding: 2.5rem;
    margin: 1.5rem;
    padding-bottom: 0;
    margin-bottom: 0;

    & .image-right{
      display: block;
      transition: transform 200ms;
      border-radius: 1.6rem;

      &:hover{
        transform: scale(1.05) rotate(2.5deg);
        box-shadow:0 8rem 8rem $shadow-color;
        transition-timing-function: ease;
      }
    }

    & .image-left{
      display: block;
      transition: transform 200ms;
      border-radius: 1.6rem;

      &:hover{
        transform: scale(1.05) rotate(-2.5deg);
        box-shadow:0 8rem 8rem $shadow-color;
        transition-timing-function: ease;
      }
    }
   
  }
