  /* CSS for Image Modals */


  /* The Modal (background) */
  .imgModal {
    display: none; /* Hidden by default */
    position: fixed; /* Stay in place */
    z-index: 1000; /* Sit on top */
    padding-top: 15px; /* Location of the box */
    left: 0;
    top: 0;
    width: 100%; /* Full width */
    height: 100%; /* Full height */
    overflow: auto; /* Enable scroll if needed */
    background-color: $grey-opacity; /* Black w/ opacity */
  }

  /* Modal Content */
  .modal-content {
    display: flex;
    flex-direction: column;
    pointer-events: auto;
    background-clip: padding-box;
    border-radius: 0.45rem;
    max-height: 740px;
    position: relative;
    outline: 0;
    background-color: $white;
    margin: auto;
    padding: 0;
    width: 80%;
    box-shadow: 0 4px 8px 0 $shadow-color,0 6px 20px 0 $shadow-color;
    -webkit-animation-name: animatetop;
    -webkit-animation-duration: 0.4s;
    animation-name: animatetop;
    animation-duration: 0.4s
  }

  /* Add Animation */
  @-webkit-keyframes animatetop {
    from {top:-300px; opacity:0} 
    to {top:0; opacity:1}
  }

  @keyframes animatetop {
    from {top:-300px; opacity:0}
    to {top:0; opacity:1}
  }

  /* The Close Button */
  .modal-header .close {
    color: $white;
    float: right;
    font-size: 28px;
    font-weight: bold;
    opacity: 1;
    text-shadow: 0 0px 0 $black;
  }

  .modal-header .close:hover,
  .modal-header .close:focus {
    color: rgb(240, 234, 234);
    text-decoration: none;
    cursor: pointer;
  }

  .modal-header {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    float: left;
    padding: 0px 16px;
    background-color: $deep-red;
    color: $white;
    border-bottom: 1px solid $border-bottom;
    border-top-left-radius: calc(0.3rem - 1px);
    border-top-right-radius: calc(0.3rem - 1px);

    p{
      margin: 0 !important;
    }

  }

  .modal-body {
    flex: 1 1 auto;
    position: relative;
  }

  .modal-footer {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: flex-end;
    padding: 16px 16px;
    background-color: $deep-red;
    color: $white;
    border-top: 1px solid $border-bottom;
    border-bottom-right-radius: calc(0.3rem - 1px);
    border-bottom-left-radius: calc(0.3rem - 1px);
  }

  #modalBtn {
    display: inline-block;
    padding: 0.3em 1.2em;
    margin:0 0.3em 0.3em 0;
    border: 2px solid $deep-red;
    border-radius:2em;
    box-sizing: border-box;
    text-decoration:none;
    font-family:'Roboto',sans-serif;
    font-weight:300;
    color:$white;
    background-color:$deep-red;
    text-align:center;
    transition: all 0.2s;
  }

  #modalBtn:hover {
    background-color:$dark-red;
    border: 2px solid $dark-red
  }


  @media all and (max-width:30em){
     #modalBtn{
      display:block;
      margin:0.2em auto;
    }
  } 
