  /* ARTICLE */

  .article {
    margin: 0 auto 5rem;

    @media only screen and (min-width: 1250px) {
      margin: 0 auto 6rem;
    }

    @media only screen and (min-width: 768px) {
      width: 76.8rem;
      min-width: 768px;
    }
  
  }
  
  .article-hero {
    margin-bottom: 2rem;
  }
  
  .article-hero__heading {
    color: $black;
    display: inline-block;
    font-size: 3.2rem;
    font-weight: 900;
    line-height: 1.2;
    margin-bottom: 2rem;
    position: relative;
    z-index: 1;
  }
  
  @media only screen and (min-width: 1250px) {
    .article-hero__heading {
      font-size: 4.2rem;
      margin-bottom: 3rem;
    }
  }
  
  .article-hero__synopsis p,.article-hero__synopsis li {
    font-size: 1.8rem;
    margin-bottom: 2.6rem;
  }
  
  @media only screen and (min-width: 1250px) {
    .article-hero__synopsis p,.article-hero__synopsis li {
      font-size: 2.2rem;
      line-height: 1.6;
    }
  }
  
  .article-hero__synopsis p:last-child,.article-hero__synopsis li:last-child {
    margin-bottom: 0;
  }
