.site-breadcrumb {
    
    position: sticky;
    top: 0;
    left: 0;
    right: 0;
    background: linear-gradient(180deg, white 0%, rgba(255, 255, 255, 0.95) 75%, rgba(255, 255, 255, 0.75) 100%);
    flex-shrink: 0;
    z-index: 2;
    border-bottom: 1px solid #e7e7e7;


    .site-breadcrumb-container {

        display: flex;
        align-items: center;
        justify-content: space-between;
        height: 100%;
        max-width: 125rem;
        margin-right: auto;
        margin-left: auto;
        padding-left: 2rem;
        padding-right: 2rem;
        position: relative;
        
        @media only screen and (min-width: 992px) {
            max-width: 115rem;
        }
            
        @media only screen and (min-width: 1250px) {
            max-width: 125rem;
        }
    
        @media only screen and (min-width: 1325px) {
            max-width: 132rem;
        }
        
        @media only screen and (min-width: 1400px) {
            max-width: 138rem;
        }
        
        @media only screen and (min-width: 1650px) {
            max-width: 144rem;
        }
        
        @media only screen and (min-width: 1920px) {
            max-width: 170rem;
        }

        /* CSS BreadCrumb Links */
        @import "header/breadCrumb/breadCrumbLinks";

        /* CSS BreadCrumb Functions */
        @import "header/breadCrumb/breadCrumbFunctions";

    }

    .scollBarIndicator-container {
        
        width: 100%;
        height: 2px;
        background: transparent;

        /* CSS scrollBarIndicator */
        @import "header/breadCrumb/scrollBarIndicator";

    }
}