.rhs-top-link {
    border-color: $grey;
    border-width: 2px;
    border-radius: 50%;
    position: fixed;
    bottom: 0;
    right: 5%;
    align-items: center;
    justify-content: center;
    display: inline-flex;
    cursor: pointer;
    margin: 0 1.5em 1.5em 0;
    padding: .25em;
    width: 35px;
    height: 35px;
    background-color: $snowwhite;
    transition: all .25s ease-in-out;
  
    -webkit-box-shadow:0.75px 0.9px 2px 2px $shadow-color;
    box-shadow:0.75px 0.9px 2px 2px $shadow-color;
    -webkit-transform:translateY(-2px);
    transform:translateY(-2px);
    
    &.show {
      visibility: visible;
      opacity: 1;
    }
    
    &.hide {
      visibility: hidden;
      opacity: 0;
    }
  
    svg {
        fill: $dark-pink;
        width: 16px;
        height: 12px;
    }
  
    &:hover {
        background-color: $dark-pink;
  
        svg {
            fill: $white;
        }
    }
  }