/* CONTENT INSIDE DOCS */

&.docs{

    /* HEADINGS */
    h1, h2, h2.likeAnH1, h3, h4, h5, h6 {
      font-size: 100%;
      font-weight: 700;
    }

    h1, h2.likeAnH1{
      overflow-wrap: break-word;
      margin-top: 0;
      margin-bottom: 0px;
      line-height: 1.3;
      font-size: 3.0rem;
      word-break: break-word;
  
      -webkit-animation: fadein 1s forwards, slidein 1.15s forwards;; /* Safari, Chrome and Opera > 12.1 */
      -moz-animation: fadein 1s forwards, slidein 1.15s forwards;; /* Firefox < 16 */
      -ms-animation: fadein 1s forwards, slidein 1.15s forwards;; /* Internet Explorer */
      -o-animation: fadein 1s forwards, slidein 1.15s forwards;; /* Opera < 12.1 */
      animation: fadein 1s forwards, slidein 1.15s forwards;

      @keyframes fadein {
        from  { opacity: 0;}
          to  { opacity: 1;}
      }

      @keyframes slidein {
        from  { transform:translateY(-25px);}
          to  { transform:translateY(0);}
      }
  
      /* Firefox < 16 */
      @-moz-keyframes fadein {
        from  { opacity: 0;}
          to  { opacity: 1;}
      }
      @-moz-keyframes slidein {
        from  { transform:translateY(-25px);}
          to  { transform:translateY(0);}
      }
  
      /* Safari, Chrome and Opera > 12.1 */
      @-moz-keyframes fadein {
        from  { opacity: 0;}
          to  { opacity: 1;}
      }
      @-moz-keyframes slidein {
        from  { transform:translateY(-25px);}
          to  { transform:translateY(0);}
      }
  
      /* Internet Explorer */
      @-ms-keyframes fadein {
        from  { opacity: 0;}
          to  { opacity: 1;}
      }
      @-ms-keyframes slidein {
        from  { transform:translateY(-25px);}
          to  { transform:translateY(0);}
      }
  
        /* Opera < 12.1 */
      @-o-keyframes fadein {
        from  { opacity: 0;}
          to  { opacity: 1;}
      }
      @-o-keyframes slidein {
        from  { transform:translateY(-25px);}
          to  { transform:translateY(0);}
      }

    }

    .metadata {
      display: block;
      color: $darkgrey;
      font-size: 1.32rem;
    }
  

    h2, h3, h4, h5, h6{
      margin-top: 10px;
      margin-bottom: 25px;
      line-height: 1.3;
    }
    
    h1:first-of-type{
      margin-top: -7px;
      margin-bottom: 0;
    }
    
    h2:first-of-type{
      margin-top: 10px;
      font-size: 2.4rem;
    }

    h2{
      margin-top: 35px;
      font-size: 2.5rem;
    }
  
    h3{
      margin-top: 35px;
      font-size: 2.0rem;
    }


    /* PARAGRAPHs */

    p{
      overflow-wrap: break-word;
      margin-top: 1.6rem;
      margin-bottom: 0;
      margin-left: 0;
      margin-right: 0;
      font-size: 1.6rem;
      line-height: 1.6;
    }

    /* LISTS */

    ol.lower-roman li{
      list-style-type:lower-roman;
  
     }

     ol.numbered-list {
      margin: 16px 0;
      margin-left: 38px;
      padding: 0;
    }
  
    ol.numbered-list li{
      margin: 0;
      padding: 0 0 0 2.6rem;
      text-indent: -2.6rem;
      list-style-type: none;
      counter-increment: item;
      font-size: 1.6rem;
    }
  
    ol.numbered-list li:before {
      display: inline-block;
      width: 1.6rem;
      padding-right: 0.8rem;
      font-weight: bold;
      text-align: right;
      content: counter(item) ".";
    }
  
    ol > li, ul > li{
      list-style: disc outside none;
    }
    
    ol, ul{
      margin: 16px 0;
      margin-left: 38px;
      padding: 0;
    }

    li {
      font-size: 1.6rem;
    }
  
    /* ALERTS */

    .alerts-font {
      font-family: 'Zilla Slab';
    }

    .alert-warning {
      color: #856404;
      background-color: #fff3cd;
      border-color: #ffeeba;
    }

    .alert {
      position: relative;
      padding: 1.2rem 2.0rem;
      margin-top: 1.6rem;
      border: 1px solid transparent;
      border-radius: 0.40rem;
    }

    /* CALLOUTS */

    .callout {
      padding: 1.0rem;
      padding-left: 1.5rem;
      margin: 20px 0;
      border: 1px solid $light-border-color;
      border-left-width: 5px;
      border-radius: 3px;
      border-top-color: transparent;
      border-right-color: transparent;
      border-bottom-color: transparent;

      &.callout-default {
        border-left-color: $dark-pink;
      }

      p{
        margin: 0px;
      }
    }

    /* CODE */

    .copy-code-container {
      box-sizing: content-box;
      font-size: 1.28rem;
      color: $body-color;
      display: flex;
      justify-content: space-between;
      background-color: $greywhite;
      margin: 1.6rem 0 -1.6rem;
      border: 1px solid $border-color;
      border-bottom: 0;
      min-height: 30px;
    }
  
    .copy-code-container > .language-name {
      padding: 2px 16px;
      flex-grow: 1;
    }
  
    .copy-code-button {
      padding: 2px 10px;
      display: flex;
      align-items: center;
      justify-content: center;
      line-height: normal;
      border: 0 solid $border-color;
      border-left-width: 1px;
      cursor: pointer;
      background-color: transparent;
      color: $body-color;
  
      &::before {
          content: "Copy";
      }
  
      &::after {
          margin-left: 4px;
          font-family: "Font Awesome 5 Free"; font-weight: 400; content: "\f0c5";
          width: 1em;
          font-size: 1.1em;
      }
  
      // This class will be toggled via JavaScript
      &.copied {
        background-color: $grassgreen;
        color: $white;
          &::before {
              content: "Copied!";
          }
  
          &::after {
              color: $white;
              content: "\2714";
              font-size: 1.1em;
          }
      }
    }

    pre {
      -moz-osx-font-smoothing: auto;
      -webkit-font-smoothing: auto;
      font-family: SFMono-Regular,Consolas,Liberation Mono,Menlo,Courier,monospace !important;
      line-height: 19px;
    }
  
    pre{
      overflow-x: auto;
      overflow-y: auto;
      overflow-wrap: normal;
      -webkit-overflow-scrolling: touch;
      padding: 1.6rem;
      margin: 1.6rem 0 0;
      word-spacing: normal;
      word-break: normal;
      font-family: monospace,monospace;
      font-size: 1.32rem;
      white-space: pre;
      -moz-tab-size: 4;
      -o-tab-size: 4;
      tab-size: 4;
      -webkit-hyphens: none;
      -ms-hyphens: none;
      hyphens: none;
      border: 1px solid $border-color;
      background-color: $snowwhite;
    }
  
    pre > code{
      display: block;
      position: relative;
      padding: 0;
      border: 0;
      line-height: 19px;
    }

    code{
      font-family: monospace,monospace;
      font-size: 1em;
      direction: ltr;
      font-family: SFMono-Regular,Consolas,Liberation Mono,Menlo,Courier,monospace;
    }
  
    :not(a):not(pre) > code{
      overflow-wrap: break-word;
      padding: .1em .2em;
      border-radius: 3px;
      font-size: 85%;
      background-color: $snowwhite;
    }

    /* IMAGES */

    figure{
      display: flex;
      align-items: center;
      justify-content: center;
      flex-direction: column;
      margin: 1.5rem 1.0rem 1.5rem 1.0rem;
      
      figcaption {
        text-align: center;
      }
    }
  
    .docimage{
      height: auto;
      padding: 2.8%;
      transition-property: all;
      transition-duration: 120ms;
      transition-timing-function: linear;
      transition-delay: 0s;
  
      &:hover{
        border-radius: 4px;
        -webkit-box-shadow:0 0px 24px 0 $shadow-color;
        box-shadow:0 0px 24px 0 $shadow-color;
      }
  
      &.no-hover{
        border-radius: 0px;
        -webkit-box-shadow:0 0px 0px 0 $shadow-color;
        box-shadow:0 0px 0px 0 $shadow-color;
      }
    }
  
    .img-wrap {
      position: relative;
    }
  
    .img-wrap:hover .img-hover-description-layer {
      visibility: visible;
      opacity: 1;
    }
  
    .img-hover-description-layer{
      position: absolute;
      top: -1.4rem;
      left: 0;
      right: 0;
      visibility: hidden;
      opacity: 0;
      display: flex;
      align-items: center;
      justify-content: center;
    }
  
    .img-wrap:hover .img-hover-description {
      transform: translateY(0);
      color: $red;
      font-size: 1.2rem;
      font-weight: 500;
    }

    p a:not(.button).xls {
      background: url(/dreamstar/assets/images/xls.png) no-repeat 0 0 transparent;
      background-size: 23px 28px;
      padding: 5px 0px 6px 35px;
      border-bottom: none;
      //display: block;

      &:hover{
        background-color: transparent;
        color: $dark-pink;
        border-bottom: none;
      }
    }

    p a:not(.button).doc {
      background: url(/dreamstar/assets/images/doc.png) no-repeat 0 0 transparent;
      background-size: 23px 28px;
      padding: 5px 0px 6px 35px;
      border-bottom: none;
      //display: block;

      &:hover{
        background-color: transparent;
        color: $dark-pink;
        border-bottom: none;
      }
    }

    /* Video Wrapper */
    
    .videoWrapper {
      position: relative;
      padding-bottom: 56.25%; /* 16:9 */
      height: 0;
      display: flex; 
      justify-content: center;
      margin: 3rem;
    }
    .videoWrapper iframe {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      border-radius: 1.0rem 1.0rem;
    }


    /* TABLES */

    table {
      margin-top: 2.0rem;
      margin-bottom: 3.0rem;
      table-layout: auto;
      width: 100%;
      font-size: 1.32rem;
      border-radius: 5px;
      transition-property: all;
      transition-duration: 120ms;
      transition-timing-function: linear;
      transition-delay: 0s;
      border-collapse: collapse;

      &:hover{
        border-radius: 5px;
        -webkit-box-shadow:0 0px 24px 0 $light-grey-opacity;
        box-shadow:0 0px 24px 0 $light-grey-opacity;
      }
  
      caption {
        font-size: 1.0rem;
        font-weight: 600;
        letter-spacing: 2px;
        text-transform: uppercase;
        padding: 1.0rem;
        padding-top: 0;
        border-width: 0 0 1px;
        border-style: solid;
        border-color: $white;
        border-bottom: transparent;
        text-align: middle;
      }
  
      td, th {
        display: table-cell;
        word-wrap: break-word;
        padding: 1.2rem 1.6rem;
        line-height: 1.5;
        vertical-align: top;
        border-top: 1px solid $white;
        border-right: 0;
        border-left: 0;
        border-bottom: 0;
        border-style: solid;
      }

      th {
        font-size: 1.8rem;
        padding-top: 12px;
        padding-bottom: 12px;
        text-align: left;
        color: $white;
      }

      th:first-of-type {
        width: 50%;
      }

      th:nth-of-type(2) {
        width: 50%;
      }
  
      td p:first-child,
      th p:first-child {
        margin-top: 0;
      }
  
      td.nowrap,
      th.nowrap {
        white-space: nowrap;
      }
  
      td.is-narrow,
      th.is-narrow {
        width: 15%;
      }
  
      th:not([scope='row']) {
        border-top: 0;
        border-bottom: 1px;
      }

      tr:hover {
        background-color: $lightgreywhite;
      }
  
    }

    /* LINK STYLING */

    p a:not(.button), ul a:not(.button), ol a:not(.button) {
      text-decoration: none;
      border-bottom: 2px solid $dark-pink;
      transition: 120ms linear;
      transition-property: background-color;
      color: $black;
      padding-left: 2px;
      padding-right: 2px;
      font-weight: 500;
  
      &:visited {
        border-bottom: 2px solid $dark-pink;
        color: $black;
      }
  
      &:hover {
        border-bottom: 2px solid $dark-pink;
        background-color: $dark-pink;
        color: $white;
      }
  
      &:active {
        border-bottom: 2px solid $dark-pink;
      }
    }

    /* THEMATIC BREAK */

    hr {
      margin-top: 1.5rem;
      border: 0.5px solid $ligher-grey;
    }

    /* SHADED CONTAINER */

    .shaded-container
    {
      margin-top: 2rem;
      margin-bottom: 2rem;
      padding: 2rem;
      padding-left: 0;
    }

} 