/** HR **/

.hr {
    border: none;
    border-top: 0.2rem solid $greywhite;
    margin: 0 0 2rem;
  }
  
  @media only screen and (min-width: 768px) {
    .hr {
      margin-top: 3rem;
      margin-bottom: 3rem;
    }
  }
  
  .hr--m-top {
    margin-top: 2rem;
  }
  
  @media only screen and (min-width: 768px) {
    .hr--m-top {
      margin-top: 4rem;
    }
  }
  
  .hr--narrow {
    margin-left: auto;
    margin-right: auto;
    width: 40%;
  }
  
  .hr--thin {
    border-width: 0.1rem;
  }
  
  .hr--light {
    border-color: $greywhite;
  }
  
  .hr--medium {
    border-color: $greywhite;
  }
  
  .hr--sidebar {
    border-color: $greywhite;
    border-width: 0.1rem;
    margin: 2rem 0;
  }
  
  @media only screen and (min-width: 768px) {
    .hr--sidebar {
      margin-top: 3rem;
      margin-bottom: 3rem;
    }
  }
  
  @media only screen and (min-width: 1920px) {
    .hr--sidebar {
      margin-top: 4rem;
      margin-bottom: 4rem;
    }
  }
  
  .hr--spacer {
    border-top: none;
    margin: 0 0 2rem;
  }
  
  .hr--spacer-tall {
    border-top: none;
    margin: 0 0 2rem;
  }
  
  @media only screen and (min-width: 1250px) {
    .hr--spacer-tall {
      margin-bottom: 3rem;
    }
  }
  
  .hr--spacer-double {
    border-top: none;
    margin: 0 0 3rem;
  }
  
  @media only screen and (min-width: 1250px) {
    .hr--spacer-double {
      margin-bottom: 4rem;
    }
  }
  
  @media only screen and (min-width: 768px) {
    .hr--half-margin {
      margin-top: 2rem;
      margin-bottom: 2rem;
    }
  }