/* BREADCRUMB FUNCTIONS */

.site-breadcrumb-container-functions {
    padding: 0 !important;
    font-size: 1.2rem;
    justify-content: space-between !important;
    flex-grow: 1 !important;

    @media screen and (max-width: 1087px), screen and (min-resolution: 120dpi) and (max-width: 1087.9px) {
      display: flex !important;
    }

    @media screen and (max-width: 767px), screen and (min-resolution: 120dpi) and (max-width: 767.9px) {
      display: none !important;
    }
}

.site-breadcrumb-container-function-list{
  display: flex !important;
  align-items: center;
  flex-direction: row !important;
  flex-wrap: wrap !important;
  flex-grow: 1 !important;
  list-style-type: none !important;
  padding: 0px !important;
  margin: 0px !important;

  @media screen and (min-width: 768px), print {
      justify-content: flex-end !important;
  }
}

.site-breadcrumb-container-function-list__item{
  margin: 0px !important;
  display: flex;
  align-items: center;
}

.site-breadcrumb-container-function-list__button{
  display: inline-flex;
  align-items: center;
  justify-content: center;
  list-style-type: none !important;
  overflow: visible;
  cursor: pointer;
  padding-bottom: calc(0.5em - 1px);
  padding-left: 1.2em;
  padding-right: 0.90em;
  padding-top: calc(0.5em - 1px);
  text-align: center;
  white-space: nowrap;

  border-radius: 2px;

  background-color: transparent;
  border-color: transparent;
  border-width: 0px;
  color: $black;
  outline-color: $black;
  transition: 0.05s;
  text-decoration: none !important;

  & .icon{
    display: inline-flex;
    justify-content: center;
    align-items: center;
    width: 1.5rem;
    height: 1.1rem;
    margin-right: 0.30rem;
    margin-left: calc(-0.375em - 1px);
    font-size: 1.4rem;
  }

  & .label{
    font-size: 1.2rem;
    @media screen and (max-width: 1087px), screen and (min-resolution: 120dpi) and (max-width: 1087.9px) {
        border: 0;
        clip: rect(1px, 1px, 1px, 1px);
        clip-path: inset(50%);
        height: 1px;
        margin: -1px;
        overflow: hidden;
        padding: 0;
        position: absolute;
        width: 1px;
        word-wrap: normal;
    }
    @media screen and (max-width: 767px) and (max-aspect-ratio: 1 / 1), screen and (min-resolution: 120dpi) and (max-width: 767.9px) and (max-aspect-ratio: 1 / 1), screen and (max-width: 767px) and (min-resolution: 120dpi) and (max-aspect-ratio: 1 / 1), screen and (min-resolution: 120dpi) and (max-width: 767.9px) and (min-resolution: 120dpi) and (max-aspect-ratio: 1 / 1) {
        display: none !important;
    }
  }

  &.colorfill{
    &:hover, &.is-hovered, &.focus-visible, &.is-focused {
      color:$white;
      background-color:$dark-pink;
      -webkit-box-shadow:0 7px 14px 0 $shadow-color;
      box-shadow:0 7px 14px 0 $shadow-color;
      border-radius: 2px;
    }
  }

}


/* SHARING MENU */

#share-menu__item {
 
  > div{
      position: relative;
  }
}

#share-menu__container {

    position: absolute;
    right: -0.8rem;
    margin-top: 8px;
    padding: 0.5rem !important;
    z-index: 100;
    width: auto;
    top: 100%;
    white-space: nowrap;
    background-color: $white;
    border: 1px solid $border-color;
    box-shadow: 0 6.4px 14.4px 0 $lightshadow-color, 0 1.2px 3.6px 0 $lightshadow-color;

     > ul{
      list-style: none;
      margin: 0;
      padding: 0;
      font-size: 1.2rem;
    }

    li {
      list-style: none;
      margin: 0;
      padding: 0;
      font-size: 1.2rem;
    }

}

.share-menu__link{
  display: flex;
  appearance: none;
  position: relative;
  align-items: center;
  justify-content: flex-start;
  text-align: center;
  overflow: visible;
  cursor: pointer;
  white-space: nowrap;

  padding-bottom: calc(0.375em - 1px);
  padding-left: 0.75em;
  padding-right: 0.75em;
  padding-top: calc(0.375em - 1px);

  border: 1px solid transparent;
  border-radius: 2px;
  border-color: transparent;
  border-width: 0px;
  box-shadow: none;
  text-decoration: none;

  line-height: 1.6;
  font-size: 1.2rem;
  width: 100%;
  height: 2.25em;

  outline-color: $black;
  color: $black;
  background-color: transparent;

  transition: 0.05s;

  & .icon{
    display: inline-flex;
    justify-content: center;
    align-items: center;
    width: 1.9rem;
    height: 1.8rem;
    margin-right: 0.40rem;
    margin-left: calc(-0.375em - 1px);
    font-size: 1.4rem;
  }

  & i.colorfill {
    color: $dark-pink;
  }

  &:hover {
    -webkit-box-shadow:0 2px 7px 0 $shadow-color;
    box-shadow:0 2px 7px 0 $shadow-color;
    -webkit-transform:translateY(-1px);
    transform:translateY(-1px);
    border-radius: 2px;

      .icon{
        background-color:$dark-pink;
        -webkit-box-shadow:0 2px 7px 0 $shadow-color;
        box-shadow:0 2px 7px 0 $shadow-color;
        -webkit-transform:translateY(-1px);
        transform:translateY(-1px);
        border-radius: 2px;
      }

      i.colorfill{
        color:$white;
      }
    } 
  
}






 



      


 

 




   