/* BREADCRUMB LINKS */

.site-breadcrumb-container-nav{
    padding: 0 !important;
    flex-grow: 1 !important;
    @media screen and (min-width: 768px), print {
        padding-left: 1.6rem !important;
        padding-right: 1.6rem !important;
    }
}

.site-breadcrumb-container-list {
    margin: 0;
    margin-top: 0.0rem;
    margin-bottom: 0.10rem;
    padding: 0;
    background: none;
    width: auto;
    width: 100%;
    list-style: none;


}

.site-breadcrumb-container-list__item{
    display: inline;
    margin: 0;
    padding: 0;
    font-size: 1.25rem;
    font-family: 'Raleway', sans-serif;
}

.site-breadcrumb-container-list__item:not(:last-of-type)::after{
    content: '/';
}

.site-breadcrumb-container-list__item::after {
    display: inline-block;
    margin: 0px 2px;
    color: $darkish-grey;
    font-weight: 600;
    font-size: 1.0rem;
}

.site-breadcrumb-container-list__link > span{
    padding: 0.45rem 0.36rem 0.45rem 0.36rem;
    width: 100%;
    font-size: 1.2rem;
    transition: 0.05s;

    &:hover{
        display: inline;
        bottom: -0.125rem;
        border-radius: 2px;
        color:$white;
        background-color: $dark-pink;
        -webkit-box-shadow:0 7px 14px 0 $shadow-color;
        box-shadow:0 7px 14px 0 $shadow-color;
        -webkit-transform:translateY(-2px);
        transform:translateY(-2px)
      }
}

.site-breadcrumb-container-list__link {
    font-weight: 600;
    color: $black;
    &.is-active{
        color: $dark-pink;
    }
}

