/* TEXT PANEL */

.text-only-panel {
    margin-bottom: 3.5rem;
  }
  
  @media only screen and (min-width: 1250px) {
    .text-only-panel {
      margin-bottom: 2rem;
    }
  }

  @media only screen and (min-width: 1920px) {
    .text-only-panel--hub-hero {
      margin-top: 2rem;
      margin-bottom: 2rem;
    }
  }

  .text-only-panel__title {
    color: $black;
    display: inline-block;
    font-size: 3rem;
    font-weight: 900;
    line-height: 1.2;
    margin-bottom: 2rem;
    position: relative;
    z-index: 1;

  }
  
  @media only screen and (min-width: 768px) {
    .text-only-panel__title {
      font-size: 4rem;
    }
  }
  
  @media only screen and (min-width: 1250px) {
    .text-only-panel__title {
      margin-bottom: 3rem;
      font-size: 4.8rem;
    }
  }

  @media only screen and (min-width: 1250px) {
    .text-only-panel__title--big {
      font-size: 4rem;
    }
  }
  
  @media only screen and (min-width: 1920px) {
    .text-only-panel__title--big {
      font-size: 5.2rem;
    }
  }

  .text-only-panel__subtitle {
    font-size: 1.4rem;
    font-weight: 700;
    line-height: 1.2;
    margin: 0 0 1rem;
  }
  
  @media only screen and (min-width: 768px) {
    .text-only-panel__subtitle {
      margin-bottom: 1.5rem;
    }
  }
  
  @media only screen and (min-width: 1920px) {
    .text-only-panel__subtitle {
      font-size: 2.2rem;
      margin-top: 1rem;
    }
  }

  .text-only-panel__synopsis p:last-child {
    margin-bottom: 0;
  }

  .text-only-panel__synopsis--big {
    margin-bottom: 3rem;
  }
  
  @media only screen and (min-width: 1920px) {
    .text-only-panel__synopsis--big {
      font-size: 1.8rem;
    }
  }
  

  .text-img-panel {
    margin-bottom: 3rem;
  }
  
  @media only screen and (min-width: 1250px) {
    .text-img-panel {
      margin-bottom: 6rem;
    }
  }

  .text-img-panel__title {
    color: $black;
    display: inline-block;
    font-size: 3rem;
    font-weight: 900;
    line-height: 1.2;
    margin-bottom: 2rem;
    position: relative;
    z-index: 1;
  }
  
  @media only screen and (min-width: 768px) {
    .text-img-panel__title {
      font-size: 4rem;
    }
  }
  
  @media only screen and (min-width: 1250px) {
    .text-img-panel__title {
      margin-bottom: 3rem;
      font-size: 4.8rem;
    }
  }

  .text-img-panel__subtitle {
    font-size: 1.4rem;
    font-weight: 700;
    line-height: 1.2;
    margin: 0 0 1rem;
  }
  
  @media only screen and (min-width: 768px) {
    .text-img-panel__subtitle {
      font-size: 2rem;
      margin-bottom: 1.5rem;
    }
  }
  
  @media only screen and (min-width: 1920px) {
    .text-img-panel__subtitle {
      font-size: 2.2rem;
      margin-top: 1rem;
    }
  }
  
  .text-img-panel__synopsis p:last-child {
    margin-bottom: 0;
  }
  
  .text-img-panel__content {
    margin-bottom: 3rem;
  }
  
  @media only screen and (min-width: 1250px) {
    .text-img-panel__content {
      float: left;
      margin-bottom: 0;
      padding-right: 1rem;
      width: 50%;
    }
  }
  
  @media only screen and (min-width: 1440px) {
    .text-img-panel__content {
      padding-right: 2rem;
    }
  }
  
  @media only screen and (min-width: 1250px) {
    .text-img-panel__img-wrap {
      float: right;
      padding-left: 1rem;
      position: relative;
      width: 50%;
    }
  }
  
  @media only screen and (min-width: 1440px) {
    .text-img-panel__img-wrap {
      padding-left: 2rem;
    }
  }
  
  .text-img-panel__img {
    background-position: 50% 50%;
    background-repeat: no-repeat;
    background-size: cover;
    display: block;
    height: 0;
    padding-top: 56.25%;
    position: relative;
    width: 100%;
  }
  