 /* ACCORDIAN */

 .accordion-item {
    border-top: 0.1rem solid $light-border-color;
    border-bottom: 0.1rem solid $light-border-color;
    margin-top: 4rem;
  }


  .accordion-item + .accordion-item {
    border-top: 0;
    margin-top: 0;
  }

  .accordion-item+p,.accordion-item+h2,.accordion-item+h3 {
    margin-top: 4rem;
  }
  
  @media only screen and (min-width: 1250px) {
    .accordion-item+p,.accordion-item+h2,.accordion-item+h3 {
      margin-top: 6rem;
    }
  }

  .accordion-item__header {
    padding: 1.6rem 5rem 1.4rem 0;
    cursor: pointer;
    position: relative;
    transition: background 300ms;
  }

  .accordion-item__h3 + p:first-of-type {
    margin: 0 !important;
  }

  .accordion-item__h2,.accordion-item__h3,.umb-grid .accordion-item__h2,.umb-grid .accordion-item__h3 {
    margin: 0 !important;
    font-weight: 700 !important;
    font-size: 2rem;
  }
  
  @media only screen and (min-width: 768px) {
    .accordion-item__h2,.accordion-item__h3,.umb-grid .accordion-item__h2,.umb-grid .accordion-item__h3 {
      font-size: 2.2rem;
    }
  }

  .accordion-item__plus,.accordion-item__minus {
    position: absolute;
    right: 0;
    top: 50%;
    font-size: 1.8rem;
    transform: translateY(-50%);
    height: 2rem;
    width: 2rem;
    display: block;
  }
  
  .accordion-item__plus::before,.accordion-item__minus::before {
    content: "";
    background: $black;
    height: 100%;
    width: 0.2rem;
    display: block;
    left: 50%;
    margin-left: -0.1rem;
    position: absolute;
  }
  
  .accordion-item__plus::after,.accordion-item__minus::after {
    content: "";
    background: $black;
    width: 100%;
    height: 0.2rem;
    display: block;
    top: 50%;
    margin-top: -0.1rem;
    position: absolute;
  }
  
  .accordion-item__minus::before {
    content: none;
  }
  
  .accordion-item__minus,.is-expanded .accordion-item__plus, .accordian-item__container {
    display: none;
  }
  
  .is-expanded .accordion-item__minus, .is-expanded .accordian-item__container {
    display: block;
  }