/* JOB PROFILE NAV */

.job-profiles-nav {
    display: none;
    background: $navgrey;
  }
  
  @media only screen and (min-width: 1250px) {
    .job-profiles-nav {
      display: block;
    }
  }
  
  .job-profiles-nav__items {
    padding: 0;
    margin: 0;
    list-style-type: none;
    overflow: hidden;
  }
  
  @media only screen and (min-width: 992px) {
    .job-profiles-nav__items {
      padding: 2rem 4rem 1.7rem;
    }
  }
  
  @media only screen and (min-width: 1920px) {
    .job-profiles-nav__items {
      padding: 3rem 14rem 2.7rem;
    }
  }
  
  .job-profiles-nav__item {
    float: left;
    margin-right: 1rem;
    margin-bottom: 1rem;
  }

  .job-profiles-btn {
    font-size: 1.8rem;
    color: $black;
    font-weight: 700;
    border: 0.3rem solid $black;
    display: inline-block;
    padding: 0.6rem 2rem;
    box-shadow: -0.4rem 0.4rem 0 $black;
    background: $white;
    position: relative;
  }
  
  .job-profiles-btn--nav-link {
    font-size: 1.6rem;
    padding: 0.6rem 1rem;
  }
  
  @media only screen and (min-width: 1440px) {
    .job-profiles-btn--nav-link {
      font-size: 1.8rem;
      padding: 0.6rem 1.6rem;
    }
  }
  
  .job-profiles-btn--reveal {
    width: 100%;
    font-size: 1.8rem;
    padding: 1.2rem 3rem 1.2rem 1.4rem;
    text-align: left;
  }
  
  @media only screen and (min-width: 992px) {
    .job-profiles-btn--reveal {
      font-size: 2.4rem;
      padding: 1.8rem 4rem 1.8rem 2.4rem;
    }
  }
  
  .job-profiles-btn:hover {
    text-decoration: none;
  }
  
  .job-profiles-btn:active {
    box-shadow: none;
    top: 0.3rem;
    left: -0.3rem;
  }
  
  .job-profiles-btn__plus {
    position: absolute;
    right: 1.4rem;
    top: 50%;
    margin-top: -0.9rem;
    font-size: 2.1rem;
    height: 1.8rem;
    width: 1.8rem;
  }
  
  @media only screen and (min-width: 992px) {
    .job-profiles-btn__plus {
      right: 2rem;
    }
  }
  
  .job-profiles-btn__plus:before,.job-profiles-btn__plus:after {
    content: "";
    position: absolute;
    background-color: $black;
    transition: transform 0.25s ease-out;
  }
  
  .job-profiles-btn__plus:before {
    top: 0;
    left: 50%;
    width: 4px;
    height: 100%;
    margin-left: -2px;
  }
  
  .job-profiles-btn__plus:after {
    top: 50%;
    left: 0;
    width: 100%;
    height: 4px;
    margin-top: -2px;
  }