/* FOOTER */

.footer {
  position: relative;
  background: $black;
  padding-top: 5rem;
  padding-bottom: 2rem;
  z-index: 1;

  @media only screen and (min-width: 768px) {
    padding-bottom: 3rem;
  }

  .site-footer__logo {
    display: block;
    height: auto;
    width: 25rem;
    margin-top: -3.0rem;
    margin-bottom: 1.0rem;
  }
  
  @media only screen and (min-width: 1250px) {
    .site-footer__logo {
      width: 30rem;
      margin-top: -3.0rem;
      margin-bottom: 1.0rem;
    }
  }

  @media only screen and (min-width: 1250px) {
    .footer__row {
      display: flex;
    }
  }

  @media only screen and (min-width: 768px) {
    .footer__column {
      height: 269px;
    }
  }

  @media only screen and (min-width: 1250px) {
    .footer__column {
      flex-grow: 1;
      width: auto;
      height: 279px;
    }
  }

  .footer__title {
    font-size: 1.3rem;
    font-weight: 700;
    line-height: 1;
    letter-spacing: 0.06rem;
    margin-bottom: 1.5rem;
    text-transform: uppercase;
  }
  
  @media only screen and (min-width: 768px) {
    .footer__title {
      margin-top: 0;
      margin-bottom: 1rem;
    }
  }
  
  @media only screen and (min-width: 1250px) {
    .footer__title {
      margin-bottom: 2rem;
    }
  }
  
  @media only screen and (min-width: 1920px) {
    .footer__title {
      font-size: 1.6rem;
    }
  }
  
  .footer__title-link {
    color: $white;
    text-decoration: none;
  }
  
  .footer__title-link:hover {
    color: $dark-pink;
  }

  .footer-list {
    list-style: none;
    margin: 0 0 3rem;
    padding: 0;
  }
  
  @media only screen and (min-width: 1650px) {
    .footer-list {
      margin-bottom: 4rem;
    }
  }
  
  .footer-list__item {
    float: left;
    font-size: 1.2rem;
    font-weight: 700;
    line-height: 1.5;
    margin: 0 0 0.8rem;
    padding-right: 1rem;
    width: 50%;
  }
  
  @media only screen and (min-width: 374px) {
    .footer-list__item {
      font-size: 1.3rem;
    }
  }
  
  @media only screen and (min-width: 768px) {
    .footer-list__item {
      font-size: 1.4rem;
      margin-bottom: 0.6rem;
      padding-right: 0;
      width: 100%;
    }
  }
  
  @media only screen and (min-width: 1250px) {
    .footer-list__item {
      width: auto;
      float: none;
    }
  }
  
  @media only screen and (min-width: 1920px) {
    .footer-list__item {
      font-size: 1.6rem;
      margin-bottom: 0.3rem;
    }
  }
  
  .footer-list__link {
    color: $darkish-grey;
  }
  
  .footer-list__link:hover {
    color: $white;
    color: $dark-pink;
    text-decoration: none;
  }

  .footer-list {
  list-style: none;
  margin: 0 0 3rem;
  padding: 0;
  }
  
  @media only screen and (min-width: 1650px) {
    .footer-list {
      margin-bottom: 4rem;
    }
  }
  
  .footer-list__item {
    float: left;
    font-size: 1.2rem;
    font-weight: 700;
    line-height: 1.5;
    margin: 0 0 0.8rem;
    padding-right: 1rem;
    width: 50%;
  }
  
  @media only screen and (min-width: 374px) {
    .footer-list__item {
      font-size: 1.3rem;
    }
  }
  
  @media only screen and (min-width: 768px) {
    .footer-list__item {
      font-size: 1.4rem;
      margin-bottom: 0.6rem;
      padding-right: 0;
      width: 100%;
    }
  }
  
  @media only screen and (min-width: 1250px) {
    .footer-list__item {
      width: auto;
      float: none;
    }
  }
  
  @media only screen and (min-width: 1920px) {
    .footer-list__item {
      font-size: 1.6rem;
      margin-bottom: 0.3rem;
    }
  }
  
  .footer-list__link {
    color: $darkish-grey;
  }
  
  .footer-list__link:hover {
    color: $white;
    color: $dark-pink;
    text-decoration: none;
  }

  .footer-social {
    font-size: 0;
    list-style: none;
    margin: 2rem 0 0;
    padding: 0;
  }
  
  @media only screen and (min-width: 768px) {
    .footer-social {
      float: right;
      margin: 1.7rem 0 0;
    }
  }
  
  .footer-social__item {
    color: $white;
    display: inline-block;
    font-size: 2.2rem;
    line-height: 1;
    margin-right: 2rem;
  }
  
  @media only screen and (min-width: 768px) {
    .footer-social__item {
      margin-right: 3.5rem;
    }
  }
  
  .footer-social__item:last-child {
    margin-bottom: 0;
  }

  .footer-social__link {
    color: $white;
  }
  
  .footer-social__link:hover {
    color: $dark-pink;
    text-decoration: none;
  }
  
  .footer-partner-logos {
    font-size: 0;
    list-style: none;
    margin: -1rem 0 0;
    padding: 0;
  }
  
  @media only screen and (min-width: 768px) {
    .footer-partner-logos {
      float: left;
      margin: 1rem 0;
    }
  }
  
  .footer-partner-logos__item {
    display: inline-block;
    line-height: 1;
    margin: 0;
    padding: 0 1rem;
    vertical-align: middle;
    width: 20%;
  }
  
  @media only screen and (min-width: 768px) {
    .footer-partner-logos__item {
      margin-right: 3rem;
      padding: 0;
      width: auto;
    }
  }
  
  @media only screen and (min-width: 1250px) {
    .footer-partner-logos__item {
      margin-right: 2rem;
    }
  }
  
  @media only screen and (min-width: 1440px) {
    .footer-partner-logos__item {
      margin-right: 3rem;
    }
  }
  
  @media only screen and (min-width: 768px) {
    .footer-partner-logos__item:last-child {
      margin: 0;
    }
  }
  
  .footer-partner-logos__link {
    display: block;
    text-decoration: none;
  }

  .footer-partner-logos__img {
    display: block;
    height: auto;
    margin: 0 auto;
    max-width: 100%;
  }

  .footer-contact {
    margin: 3rem 0 0;
    padding: 0;
  }

  .footer-contact {
    font-size: 0;
    list-style: none;
    margin: 0;
  }
  
  @media only screen and (min-width: 992px) {
    .footer-contact {
      margin-top: 4rem;
      margin-bottom: 0.6rem;
    }
  }

  .footer-contact__item {
    color: $darkish-grey;
    font-size: 1.2rem;
    font-weight: 700;
    line-height: 1.8;
    margin-bottom: 0.4rem;
  }
  
  @media only screen and (min-width: 374px) {
    .footer-contact__item {
      font-size: 1.3rem;
    }
  }
  
  @media only screen and (min-width: 768px) {
    .footer-contact__item {
      border-right: 0.1rem solid $grey;
      display: inline-block;
      font-size: 1.4rem;
      line-height: 1;
      margin-right: 0.8rem;
      padding-right: 0.8rem;
      margin-bottom: 0.8rem;
    }
  }
  
  @media only screen and (min-width: 992px) {
    .footer-contact__item {
      font-size: 1.4rem;
      margin-right: 1.2rem;
      padding-right: 1.2rem;
    }
  }
  
  @media only screen and (min-width: 1920px) {
    .footer-contact__item {
      font-size: 1.6rem;
      margin-right: 1.8rem;
      padding-right: 1.8rem;
    }
  }
  
  @media only screen and (min-width: 768px) {
    .footer-contact__item--last {
      border-right: none;
      margin-right: 0;
      padding-right: 0;
    }
  }

  .footer-contact__link {
    color: $darkish-grey;
    text-decoration: none;
  }
  
  .footer-contact__link:hover {
    color: $dark-pink;
    text-decoration: none;
  }

}








