/* Search Filter */

.left-sidebar__searchFilter {
  display: inline-block;
  position: relative;

  .left-sidebar__searchBarContainer {
    position: relative;
    font-size: 1.0rem;
    text-align: left;
  }

  .left-sidebar__searchBarContainer.has-icons-left .input {
    padding-left: 2.25em;
  }

  .left-sidebar__searchBarContainer .left-sidebar__icon {
    z-index: 4;
    position: absolute;
    top: 0;
    height: 3.0em;
    width: 2.8em;
    color: $border-color;
    pointer-events: none;
    display: flex;
    justify-content: center;
    align-items: center;

    &.has-icons-left {
      left: 0;
    }

    .left-sidebar__icon-icon{
      display: inline-block;
      height:1.44rem;
      width: 1.6rem;
      text-transform: none;
      text-align: center;
    }
  }

  .left-sidebar__searchFilter-input {
    position: relative;
    appearance: none;
    text-align: left;
    align-items: center;
    vertical-align: top;
    justify-content: flex-start;
    line-height: 1.5;
    padding-right: 2.6rem;
    padding-left: 1.9em;
    padding-bottom: calc(0.375em - 1px);
    padding-top: calc(0.375em - 1px);
    margin: 0;
    overflow: visible;
    font-size: 1.4rem;
   
    border-radius: 4px;
    display: inline-flex;
    max-width: 100%;
    height: 3.0rem;
  
    box-shadow: inset 0 1px 2px $lightshadow-color;
    border: 1px solid transparent;
    border-color: $light-border-color;
   
    background-color: $white;
    color: $body-color !important;

    &:active, &:focus {
      box-shadow: 0 0 0 0.125rem $dark-pink;
      border-color: $dark-pink;
      outline-color: $dark-pink;
      outline-style: solid !important;
      outline-offset: 0 !important;
      outline: none !important;
    }
    
    &:hover {
      border-color:  $dark-pink;
      z-index: 2;
    }

  }

  .left-sidebar__searchFilter-suggestions {
    list-style-type: none;
    z-index: 1;
    position: absolute;
    max-height: 18rem;
    min-width: 100%;
    max-width: 100%;
    padding: 0;
    margin: 0.5px 0 0 !important;
    cursor: default;
    box-shadow: 0 1.6px 3.6px 0 $lightshadow-color,0 .3px .9px 0 $lightshadow-color;
    border: 0.5px solid $border-color;
    background-color: $white;

    @media screen and (min-width: 768px), print {
        max-width: 500px;
    }

    .left-sidebar__searchFilter-suggestion {
      display: block;
      border: 1px solid transparent;
      margin: 0rem;
      a {
        color: $body-color;
        text-decoration: none;
      } 
      &:hover{
        background-color: $greywhite;
        border: 1px solid $white;
      }
    }
  }
}