/* JOB PROFILE PANEL */

    .job-profile-panel-wrap {
    margin-bottom: -2rem;
    }

    @media only screen and (min-width: 1440px) {
        .job-profile-panel-wrap {
            margin-bottom: -4rem;
        }
    }

    .job-profile-panel {
        display: block;
        margin-bottom: 2rem;
        padding-bottom: 60%;
        position: relative;
        text-decoration: none;
        overflow: hidden;
        transition: all 400ms;

        &:hover{
          -webkit-box-shadow:0 10px 40px 0 $shadow-color;
          box-shadow:0 10px 40px 0 $shadow-color;
          transition: all 400ms;
          transform: rotate(1deg) scale(1) translateY(-1%) translateX(1%);
        }
    }

    @media only screen and (min-width: 768px) {
        .job-profile-panel {
            margin-bottom: 2rem;
        }
    }

    @media only screen and (min-width: 992px) {
        .job-profile-panel {
            margin-bottom: 3rem;
        }
    }

    @media only screen and (min-width: 1440px) {
        .job-profile-panel {
            margin-bottom: 4rem;
        }
    }

    .job-profile-panel--film-and-tv-drama {
        background: $green;
    }

    .job-profile-panel--visual-effects-vfx {
        background: $yellow-orange;
    }

    .job-profile-panel--games {
        background: $violet;
    }

    .job-profile-panel--animation {
        background: $light-green;
    }

    .job-profile-panel--unscripted-tv {
        background: $light-blue;    
    }

    .job-profile-panel__img {
        display: block;
        height: auto;
        width: 100%;
        position: absolute;
        left: 0;
        right: 0;
        bottom: 0;
        transition: transform 500ms;
    }
    
    @media only screen and (min-width: 992px) {
        .job-profile-panel:hover .job-profile-panel__img {
        transform: scale(1.05);
        }
    }
  
  .job-profile-panel__content {
    padding: 2rem 2rem 0;
  }
  
  @media only screen and (min-width: 768px) {
    .job-profile-panel__content {
      padding: 2rem 2rem 0;
    }
  }
  
  @media only screen and (min-width: 1250px) {
    .job-profile-panel__content {
      padding: 2.5rem 3rem 0;
    }
  }
  
  .job-profile-panel__title {
    color: #000;
    font-size: 2rem;
    font-weight: 800;
    line-height: 1.4;
    margin-bottom: 1rem;
    position: relative;
    z-index: 1;
  }
  
  @media only screen and (min-width: 768px) {
    .job-profile-panel__title {
      font-size: 3.2rem;
      line-height: 1.1;
    }
  }
  
  @media only screen and (min-width: 992px) {
    .job-profile-panel__title {
      font-size: 2rem;
      line-height: 1.4;
    }
  }
  
  @media only screen and (min-width: 1250px) {
    .job-profile-panel__title {
      font-size: 3.2rem;
      line-height: 1.1;
    }
  }
  
  .job-profile-panel__synopsis {
    color: #000;
    font-weight: 400;
    font-size: 1.4rem;
    line-height: 1.8;
    margin: 0;
  }
  
  @media only screen and (min-width: 768px) {
    .job-profile-panel__synopsis {
      font-size: 1.6rem;
    }
  }