/* LIST PANEL */

.list-panel-wrap {
    margin-bottom: -2rem;
  }

  @media only screen and (min-width: 1440px) {
    .list-panel-wrap {
      margin-bottom: -4rem;
    }
  }

  .list-panel {
    border-bottom: 0.2rem solid $tingegrey;
    display: block;
    margin-bottom: 2rem;
    padding-bottom: 1.8rem;
    position: relative;
    text-decoration: none;
    transition: all 400ms;

    &:hover{
      transition: all 400ms;
      transform: rotate(0deg) scale(1.02) translateY(-1%) translateX(1%);
    }
  }
  
  @media only screen and (min-width: 768px) {
    .list-panel {
      border-bottom: none;
      padding-bottom: 0;
      margin-bottom: 3rem;
    }
  }
  
  @media only screen and (min-width: 1440px) {
    .list-panel {
      margin-bottom: 4rem;
    }
  }
  
  div:last-child>.list-panel {
    border-bottom: 0;
  }

  .list-panel__img {
    display: block;
    height: auto;
    margin-bottom: 1.5rem;
    width: 100%;
  }

  @media only screen and (min-width: 768px) {
    .list-panel--no-img .list-panel__title:after {
      background: $tingegrey;
      content: '';
      display: block;
      height: 4rem;
      left: 0;
      position: absolute;
      top: -2.5rem;
      width: 100%;
      z-index: -1;
    }
  }

  .list-panel__title {
    color: $black;
    font-size: 1.6rem;
    font-weight: 800;
    line-height: 1.4;
    margin-bottom: 0;
    position: relative;
    z-index: 1;
  }
  
  @media only screen and (min-width: 768px) {
    .list-panel__title {
      font-size: 2rem;
      line-height: 1.3;
    }
  }
  
  @media only screen and (min-width: 1440px) {
    .list-panel__title {
      font-size: 2.4rem;
    }
  }

  .list-panel:hover .list-panel__title {
    color: $dark-pink;
    text-decoration: none;
  }

  .list-panel__synopsis {
    color: $black;
    font-weight: 400;
    font-size: 1.4rem;
    line-height: 1.8;
    margin: 0.8rem 0 0 0;
  }
  
  @media only screen and (min-width: 768px) {
    .list-panel__synopsis {
      font-size: 1.6rem;
    }
  }