/* CSS RESET */

html *, html a, html li {
    box-sizing: border-box;
    text-rendering: optimizeLegibility;
    outline-color: $outline-color;
  }
  
html {
  font-family: 'Montserrat',sans-serif;
  font-size: 62.5%;
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
  scroll-behavior: smooth
}

body {
  color: $body-color;
  min-height: 100vh;
  overflow-x: hidden;
  text-size-adjust: 100%;
  text-rendering: optimizeLegibility;
  scroll-behavior: smooth; 
  display:block;
  position:relative;
  width: 100%;
  margin: 0px;
  padding: 0px;
  border: 0px;
  font-size: 1.6rem;
  font-weight: 400;
  line-height: 180%;


  //* COLOR THEMES *//


  /* GREEN THEME */

    // Header Elements //

    &.theme-green .main-header {
      background: $grassgreen;
    }
  
    &.theme-green .site-header__logo-link {
      .overlay {
        background: $grassgreen !important;
      }
    }
  
    &.theme-green .secondary-nav__link {
      color: $black !important;
    }
    
    &.theme-green .secondary-nav__link:hover, &.theme-green .secondary-nav__item--current .secondary-nav__link {
      border-bottom: 1px solid $grassgreen !important;
    }
  
    &.theme-green .slide-out {
      background: $grassgreen !important;
    }
  
    // Footer Elements //
  
    &.theme-green .site-feedback {
      background: $grassgreen !important;
    }
  
    &.theme-green .feedback-submit__btn:hover, &.theme-green .feedback-view__btn:hover {
      color: $grassgreen !important;
    }

    // BreadCrumb Elements //

    &.theme-green .site-breadcrumb-container-list__link {
      &.is-active{
          color: $darker-green !important;
      }
    }

    &.theme-green  .site-breadcrumb-container-list__link > span{
      &:hover{
        color:$white !important;
        background-color: $grassgreen !important;
      }
    }

    &.theme-green .site-breadcrumb-container-function-list__button{
      &.colorfill{
        &:hover, &.is-hovered, &.focus-visible, &.is-focused {
          color:$white !important;
          background-color: $grassgreen !important;
        }
      }
    }

    &.theme-green .share-menu__link{
      & i.colorfill {
        color: $grassgreen !important;
      }
      &:hover {
        .icon{
          background-color: $grassgreen !important;
        }
        .colorfill{
          color: $white !important;
        }
      }
    }

    &.theme-green .scrollBarIndicator-container-bar{
      background: $grassgreen !important;
    }

    // Left Hand Side Menu Elements //

    &.theme-green ul.lhs-tree-menu-toc {
      li{
        &.is-active, &.is-active a {
          background-color: $shaded-green !important;
          color: $grassgreen !important;
        }
      }
    }

    &.theme-green .left-sidebar__searchFilter-input{
      &:active, &:focus{
        box-shadow: 0 0 0 0.125rem $dark-green !important;
        border-color: $dark-green !important;
        outline-color: $dark-green !important;
      }
      &:hover {
        border-color:  $dark-green !important;
      }
    }


    // LHS Menu Back Button Elements //
  
    &.theme-green #contentEarlierPage{
      color: $white !important;
      background-color: $dark-green !important;
      &:hover{
        background-color: $white !important;
        color: $violet !important;
        border: 1px solid $dark-green !important;
      }
      &.is-faded{
        background-color: $tingegrey !important;
        color: $grey !important;
          &:hover{
            background-color: $tingegrey !important;
            color: $grey !important;
            border: 1px solid $darkish-grey !important;
          }
      }
    }


    // Right Hand Side Menu Elements //

    &.theme-green #affixed-right-sidebar {
      li {
        &.selected {
          font-weight: 600;
          border-color: $darker-dark-green !important;

          a, a:visited {
            color: $darker-dark-green !important;
          }
        }
      }
      a, a:visited {
        color: $black !important;
      }

    }
    
    &.theme-green #right-sidebar-title {

      .right-sidebar-heading{  
        &:hover{
          color: $darker-dark-green !important;
        }
      }
      &:hover {
        border: 0.8px solid $darker-dark-green !important;
        }
    }

    &.theme-green .fa-file-alt {
      color: $darker-dark-green !important;
    }

    
    // Scroll To Top Elements // 

    &.theme-green .rhs-top-link {
      svg {
        fill: $grassgreen !important;
      }
    
      &:hover {
          background-color: $grassgreen !important;
          svg {
              fill: $white !important;
          }
      }
    }


    // Content Elements //

    &.theme-green .colour-splash {
      background: -moz-linear-gradient(top,transparent 60%,$dark-green 50%) !important;
      background: -webkit-linear-gradient(top,transparent 60%,$dark-green 50%) !important;
      background: linear-gradient(to bottom,transparent 60%,$dark-green 50%) !important;
      display: inline;
    }
  
    &.theme-green .text-img-panel__synopsis a {
      color: $dark-green !important;
    }
  
    &.theme-green .text-only-panel__synopsis a {
      color: $dark-green !important;
    }
  
    &.theme-green .list-panel:hover .list-panel__title {
      color: $dark-green !important;
    }
  
    &.theme-green .article-hero__synopsis a {
      color: $dark-green !important;
    }
  
    &.theme-green .umb-grid h1 a,.umb-grid h2 a,.umb-grid h3 a,.umb-grid h4 a,.umb-grid h5 a,.umb-grid h6 a {
      color: $dark-green !important;
    }
  
    &.theme-green .umb-grid ul li a,.umb-grid ol li a {
      color: $dark-green !important;
    }
  
    &.theme-green .umb-grid a {
      color: $dark-green !important;
    }
  
    &.theme-green a.btn--primary{
      color: $white !important;
      background: $dark-green !important;
    }
  
    &.theme-green a.btn--primary:hover {
      background: $black !important;
      color: $white !important;
    }
  
    &.theme-green .callout {
      &.callout-default {
        border-left-color: $dark-green !important;
      }
    }
  
    &.theme-green .content-wrap.docs {
      & p a:not(.button), ul a:not(.button), ol a:not(.button) {
        color: $darker-dark-green !important;
        border-bottom: 2px solid $darker-dark-green !important;
  
        &:visited {
          border-bottom: 2px solid $darker-dark-green !important;
        }
  
        &:hover {
          border-bottom: 2px solid $dark-green !important;
          background-color: $dark-green !important;
          color: $white !important;
        }
  
        &:active {
          border-bottom: 2px solid $darker-dark-green !important;
        }
  
      }
    }
  
    &.theme-green hr {
      border: 0.5px solid $dark-green !important;
    }
  
    &.theme-green .shaded-container
    {
      background-color: $shaded-green !important;
    }
  
  
    
    // Content Nav Bottom Elements // 
  
    &.theme-green #contentNextPage, &.theme-green #contentPreviousPage {
      background-color: $grassgreen !important;
      color: $white !important;
      &:hover{
        background-color: $white !important;
        color: $green !important;
        border: 1px solid $grassgreen !important;
      }
  
      &.is-faded{
      background-color: $tingegrey !important;
      color: $grey !important;
        &:hover{
          background-color: $tingegrey !important;
          color: $grey !important;
          border: 1px solid $darkish-grey !important;
        }
      }
    }
  


